<template>
  <el-tree
    ref="tree"
    :data="treeList"
    :props="leftProps"
    icon-class="el-icon-arrow-right"
    :current-node-key="currentNodeKey"
    :node-key="mid"
    :default-expand-all="false"
    :default-expanded-keys="expandedKeys"
    accordion
    @current-change="currentChange"
    @node-collapse="nodeCollapse"
  ></el-tree>
</template>

<script>
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {assign} from "_platform/pc/dormitory/func";

export default {
  name: "treeList",
  inject:['leftConfigPointer'],
  props: {
    treeList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      //
      apiTree: null,
      leftProps: null,
      level: null,

      //
      defaultChosen: null,    //
      currentNodeKey: "",     //key
      expandedKeys: [],
    }
  },
  computed: {
    leftConfig(){
      return this.leftConfigPointer()
    },
    "mid"() {
      return this.leftProps.id
    },
    "mtype"() {
      return this.leftProps.type
    },
    "mchildren"() {
      return this.leftProps.children
    }
  },
  watch: {
    treeList() {
      this.$nextTick(() => {
        let buildingId = dsf.url.queryString("buildingId");
        if (buildingId) {
          this.currentNodeKey = buildingId;
        } else {
          this.currentNodeKey = this.defaultChosen[this.mid];
        }
      })
    },
    currentNodeKey(val) {
      console.log("currentNode")
      this.$refs.tree.setCurrentKey(val);
      const data = this.$refs.tree.getCurrentNode();
      this.$store.commit("dormitory/currentFloor", {
        buildingId: data[this.mid],
        type: data[this.mtype]
      });
    }
  },
  created() {
    // Object.assign(this, this.leftConfig);   //
    assign(this, this.leftConfig);
    this.queryTree();
  },
  mounted() {
    eventBus.on(eventBusEvent.treeNextLevel, q => {
      let nextLevel;
      const data = _.cloneDeep(this.treeList);
      const recurData = list => {
        for (let i = 0; i < list.length; i++) {
          let item = list[i];
          let children = item[this.mchildren];
          if (_.isArray(children) && children.length > 0) {
            if (q.id === item[this.mid]) {
              nextLevel = children
            }
            recurData(children)
          }
        }
      }
      recurData(data)
      q.callback(nextLevel);
    })

    eventBus.on(eventBusEvent.treeCurrent, q => {
      let current;
      const data = _.cloneDeep(this.treeList);
      const recurData = list => {
        for (let i = 0; i < list.length; i++) {
          let item = list[i];
          let children = item[this.mchildren];

          if (q.id === item[this.mid]) current = item;
          if (_.isArray(children) && children.length > 0) {
            recurData(children)
          }
        }
      }

      recurData(data)
      q.callback(current);
    })
  },
  methods: {
    queryTree() {
      const computeTree = list => {
        let mlevel = 0;
        const expandArr = [];     //
        let defaultChosen;        //

        const recurData = list => {
          for (let i = 0; i < list.length; i++) {
            let item = list[i];
            if (_.isArray(item[this.mchildren]) && item[this.mchildren].length > 0) {
              //
              if (mlevel < this.level) {
                mlevel += 1
                item.mlevel = mlevel;
                expandArr.push(item[this.mid])
              }
              if (mlevel === this.level) {
                !defaultChosen && (defaultChosen = item)
              }

              recurData(item[this.mchildren])
            } else {
              item.mlevel = mlevel + 1;
              item.isLeaf = true;
              !defaultChosen && (defaultChosen = item)
            }
          }
        }
        recurData(list);
        return {
          expandArr: expandArr,
          defaultChosen: defaultChosen,
        }
      }

      this.$http.get(this.apiTree.url)
        .done(res => {
          let data = res.data;
          if (_.isFunction(this.apiTree.callback)) {
            data = this.apiTree.callback(data);
          }

          //
          if (_.isNumber(this.level) && this.level > 0) {
            const result = computeTree(data);
            this.expandedKeys = result.expandArr;
            this.defaultChosen = result.defaultChosen;
          }

          this.$emit("update:treeList", data);
        })
        .error(() => {
          dsf.layer.message("", false)
        })
    },
    currentChange(data) {
      this.$store.commit("dormitory/currentFloor", {
        buildingId: data[this.mid],
        type: data[this.mtype]
      });
    },
    nodeCollapse(data, node){
      //()
      if (node.level === 1){
        node.childNodes.forEach(a => {
          a.expanded = false
        })
      }
    }
  }
}
</script>