<template>
  <div class="ds-dormitory-container">
    <left-menu :title="leftMenuTitle">
      <template #inner>
        <component ref="left" :is="dynamicLeft" v-bind.sync="commonProps"></component>
      </template>
    </left-menu>
    <div class="ds-dormitory-right">
      <div class="top-tool-container">
        <component ref="top" :is="dynamicTop" v-bind.sync="commonProps"></component>
      </div>
      <component ref="room" :is="dynamicRoom" v-bind.sync="commonProps"></component>
    </div>
  </div>
</template>

<script>
import eventBus from "_platform/designer/seat/util/eventBus";
import {customAssign, DateFormat} from "_platform/pc/dormitory/func";
import leftMenu from "_platform/pc/dormitory/room/leftMenu";
import guestsTopTool from "_platform/pc/dormitory/room/guestsTopTool";
import resetGroupTopTool from "_platform/pc/dormitory/room/resetGroupTopTool";
import treeList from "_platform/pc/dormitory/room/treeList";
import roomBox from "_platform/pc/dormitory/room/roomBox";
import groupTopTool from "_platform/pc/dormitory/room/groupTopTool";
import groupList from "_platform/pc/dormitory/room/groupList";
import groupRoomBox from "_platform/pc/dormitory/room/groupRoomBox";
import resetGroupRoomBox from "_platform/pc/dormitory/room/resetGroupRoomBox";
import allocationTopTool from "_platform/pc/dormitory/room/allocationTopTool";
import allocationRoomBox from "_platform/pc/dormitory/room/allocationRoomBox";
import stateRoomBox from "_platform/pc/dormitory/room/stateRoomBox";
import ConfigBuilder from "_platform/designer/seat/util/configBuilder.js";
  
export default {
  name: "DsfDormitory",
  provide() {
    return {
      dormitoryRoot: this,
      leftConfigPointer: () => this.leftConfig,
      rightConfigPointer: () => this.rightConfig
    };
  },
  components: {
    leftMenu
  },
  data() {
    return {
      leftMenuTitle: "",
      //props
      commonProps: {
        queryTime: {}, //
        treeList: [], //
        personObj: {}, //
        personList: [], //
      },
        
      //
      dynamicLeft: null,
      dynamicTop: null,
      dynamicRoom: null,
        
      //
      leftConfig: null,
      rightConfig: null
    };
  },
  watch: {
    "$route.fullPath"() {
      if (getRightLayoutConfig) {
        this.rightConfig = new ConfigBuilder(getRightLayoutConfig(), this.$route.query.opt);
      }
    },
    "$route.query.opt"() {
      this.createDormitory()
    }
  },
  methods: {
    reset() {
      loop(this, 'reset');
    },
    initGuestLayout() {
      this.dynamicTop = guestsTopTool;
      this.dynamicLeft = treeList;
      this.dynamicRoom = roomBox;
    },
    initGroup() {
      this.dynamicTop = groupTopTool;
      this.dynamicLeft = groupList;
      this.dynamicRoom = groupRoomBox;
    },
    initAllocation() {
      this.dynamicTop = allocationTopTool;
      this.dynamicLeft = treeList;
      this.dynamicRoom = allocationRoomBox;
    },
    initStatus() {
      this.dynamicTop = guestsTopTool;
      this.dynamicLeft = treeList;
      this.dynamicRoom = stateRoomBox;
    },
    initResetGroup() {
      this.dynamicTop = resetGroupTopTool;
      this.dynamicLeft = groupList;
      this.dynamicRoom = resetGroupRoomBox;
    },
    renderInner() {
      //Date 
      DateFormat();
      this.commonProps.queryTime = getDefaultTime.call(this);
      const q = this.$route.query.opt;
      console.log(q)
      switch (q) {
      case "guests":
        this.initGuestLayout();
        break;
      case "group":
        this.initGroup();
        break;
      case "allocation":
        this.initAllocation();
        break;
      case "roomStatus":
        this.initStatus();
        break;
      case "resetGroup":
        this.initResetGroup();
        break;
      }
        
      const {title} = this.leftConfig;
      const {roomStateList} = this.rightConfig;
      this.leftMenuTitle = title;
      this.$store.commit("dormitory/changeRoomStateList", roomStateList)
        
      this.$nextTick(() => {
        $(window).off("scroll").on("scroll", function () {
          let left = $(window).scrollLeft();
          $(".top-tool-container").css("margin-left", -left);
        });
      });
    },
    createDormitory() {
      eventBus.clear();   //
      this.$store.commit("dormitory/activeTeamId", "")
      this.leftConfig = new ConfigBuilder(leftTreeConfig, this.$route.query.opt);
      this.rightConfig = new ConfigBuilder(getRightLayoutConfig(), this.$route.query.opt);
      this.renderInner();
    },
  },
  created() {
    const leftConfigUrl = dsf.url.getWebPath("$/seat_config/dormitory/leftTree.js");
    const rightLayoutUrl = dsf.url.getWebPath("$/seat_config/dormitory/rightLayout.js");
    this.$http.importFiles([leftConfigUrl, rightLayoutUrl]).then((res) => {
      this.createDormitory()
    });
  }
};
  
function getDefaultTime() {
  const sys_start = dsf.config.setting_sgcp_sdate;
  const sys_end = dsf.config.setting_sgcp_edate;
  const toDay = new Date().Format("yyyy-MM-dd");
    
  const tomorrow = new Date(new Date().getTime() + 24 * 60 * 60 * 1000).Format("yyyy-MM-dd");
  const mStart = sys_start ? toDay + " " + sys_start : toDay + " " + "00:00:00";
  const mEnd = sys_end ? tomorrow + " " + sys_end : toDay + " " + "23:59:59";
    
  let originTime = {
    sDate: mStart,
    eDate: mEnd
  };
    
  const {defaultQueryByUrl: defaultQuery} = this.rightConfig;
  let mTime = {
    sDate: decodeURIComponent(dsf.url.queryString(defaultQuery["startTime"])),
    eDate: decodeURIComponent(dsf.url.queryString(defaultQuery["endTime"]))
  };
    
  return customAssign(originTime, mTime);
}

function loop(map, funName) {
  Object.values(map.$refs).forEach(vm => {
    if (vm?.$refs) {
      loop(vm, funName);
        vm[funName]?.()
    }
  });
}
</script>