<template>
  <div v-if="showSelect" class="select-box">
    <el-select
      v-for="(comp, co) in selectList"
      v-model="comp.value"
      :key="co"
      @change="changeSelect(co)"
      :placeholder="comp.placeholder"
    >
      <el-option
        v-for="(item, it) in comp.option"
        :key="item.buildingId"
        :value="item.buildingId"
        :label="item.name"
      >
      </el-option>
    </el-select>
  </div>
</template>

<script>
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {assign} from "_platform/pc/dormitory/func";

export default {
  name: "inlineSelectBox",
  inject: ['rightConfigPointer'],
  computed: {
    rightConfig(){
      return this.rightConfigPointer()
    }
  },
  data() {
    return {
      //
      selectList: [],
        
      //
      showSelect: false,
      currentSelect: null,    //
      firstTime: true,    //
    }
  },
  created() {
    let predis = dsf.url.queryString("predis");
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig);
    //
    if (predis === "1") {
      this.getFirstArea();     //groupRoomBox
      return;
    }
      
    this.showSelect = true;
    this.selectList.forEach(item => {
      item.value = "";
      item.option = []
    })
    this.queryBuildList();
  },
  methods: {
    queryBuildList(num = -1) {
      let next = num + 1
      if (!this.selectList[next]) return;
        
      let q = {};
      if (this.currentSelect) {
        q = {
          buildingId: this.currentSelect.buildingId,
          type: +this.currentSelect.typeValue + 1
        }
      }
      this.$http.get(this.apiBuildingList.url, q).done(res => {
        let obj = this.selectList.slice(next, next + 1);
        // ""
        if (next === 1) {
          res.data.unshift({
            buildingId: this.selectList[next-1].value,
            name: "",
            typeText: "",
            typeValue: 0
          })
        }
          
        // ""
        if (next === 2){
          res.data.unshift({
            buildingId: this.selectList[next-1].value,
            name: "",
            typeText: "",
            typeValue: 1
          })
        }
        obj.option = res.data;
        this.selectList.splice(next, 1, obj);
        //, ()
        if (this.currentSelect?.name === "" && this.currentSelect?.typeValue === 0){
          this.selectList[2].option = []
        }
          
        //
        if (this.firstTime){
          this.firstTime = false;
          this.selectList[0].value = res.data[0].buildingId;
          this.changeSelect(0)
        }
      })
    },
    changeSelect(num) {
      this.currentSelect = this.selectList[num].option.find(a => a.buildingId === this.selectList[num].value);
      eventBus.emit(eventBusEvent.queryRoomList, this.currentSelect);
      //
      for (let i = 0; i < this.selectList.length; i++) {
        if (i <= num) continue;
        let obj = this.selectList.slice(i, i + 1);
        obj.option = [];
        obj.value = "";
        this.selectList.splice(i, 1, obj);
      }
      //
      this.queryBuildList(num);
    },
    getFirstArea(){
      this.$http.get(this.apiBuildingList.url).done(res => {
        let firstArea = res.data[0];
        eventBus.emit(eventBusEvent.queryRoomList, firstArea)
      })
    },
  }
}
</script>