// Date Date String
// (M)(d)(h)(m)(s)(q)  1-2 
// (y) 1-4 (S) 1 ( 1-3 )
// 
// (new Date()).Format("yyyy-MM-dd hh:mm:ss.S") ==> 2006-07-02 08:09:04.423
// (new Date()).Format("yyyy-M-d h:m:s.S")      ==> 2006-7-2 8:9:4.18
const DateFormat = () => {
  if (_.isFunction(Date.prototype.Format)) return;
  Date.prototype.Format = function (fmt) {
    const o = {
      "M+": this.getMonth() + 1, //
      "d+": this.getDate(), //
      "h+": this.getHours(), //
      "m+": this.getMinutes(), //
      "s+": this.getSeconds(), //
      "q+": Math.floor((this.getMonth() + 3) / 3), //
      "S": this.getMilliseconds() //
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (let k in o)
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  }
}

const customAssign = (source, target) => {
  const __custom = (old, other) => {
    return other ? other : old
  }
  return _.assignWith({}, source, target, __custom)
}

const filterOriginFloor = (originFloorList, roomStateList) => {
  if (!originFloorList || originFloorList.length === 0) return [];
  
  const arr = [];
  originFloorList.forEach(build => {
    const obj = Object.assign({}, build);
    obj.roomDtos = build.roomDtos.filter(item => {
      const state = roomStateList.find(a => a.val === item.statusValue);
      return state? state.checked: false
    })
    arr.push(obj);
  })
  
  return arr
}

const assign = (obj, config, filter = []) => {
  for (let k in config) {
    if (filter.indexOf(k) === -1) {
      obj[k] = config[k];
    }
  }
}

export {
  DateFormat,
  customAssign,
  filterOriginFloor,
  assign
}
