<template>
  <div>
    <div v-for="(floor, fl) in floorList" :key="fl" class="floor-box">
      <div class="floor-title">
        <div v-if="multipleChoose" class="check-box" @click.stop="checkAll(floor)">
          <i class="iconfont" :class="pCheckCss(floor)"></i>
          <span></span>
        </div>
        <span>{{floor.buildingText}} / {{floor.title}}</span>
        <span></span>
        <span class="it">{{floor.single}}</span>
        <span class="it">{{floor.standard}}</span>
        <span class="it">{{floor.suite}}</span>
        <span class="it">{{floor.mul}}</span>
        <span></span>
      </div>
      <div @click="emptyPlace" class="room-container">
        <div
          v-for="(room, ro) in floor[mchildren]"
          class="room"
          :class="roomCss(room)"
          :style="roomSty(room)"
          :key="ro"
          :data-id="room[mroomId]"
          v-loading="judgeLoading(room[mroomId])"
          @click.stop="clickRoom(room)"
        >
          <div v-if="roomCheckbox" class="check-box" @click.stop="checkRoom(room)">
            <i class="iconfont" :class="checkCss(room)"></i>
          </div>
          <div class="num" :style="numSty(room)">{{room[mroomNum]}}</div>
          <div class="inner">
            <p>{{room.purposeText}}</p>
            <p><i v-show="isAllocatedRoom(room.reserveId)" class="iconfont icon-dui1"></i>{{room.roomText}}</p>
            <p v-if="!room.sexValue"></p>
            <p v-else-if="parseInt(room.sexValue) === 1"></p>
            <p v-else-if="parseInt(room.sexValue) === 0"></p>
          </div>
        </div>
        <div v-for="item in appendOcc" :key="fl+'_'+item" class="occ"></div>
      </div>
    </div>
  </div>
</template>

<script>
import {assign} from "_platform/pc/dormitory/func";

export default {
  inject: ['rightConfigPointer'],
  name: "floorBox",
  props: {
    floorList: {
      type: Array,
      default() {
        return []
      }
    },
    chosenRoomId: {
      type: Array,
      default() {
        return []
      }
    },
    multipleChoose: {
      type: Boolean,
      default: false
    },
    loadingRoomId: {
      type: Array,
      default() {
        return []
      }
    },
    activeRoomId: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      roomCheckbox: true,
        
      //
      appendOcc: [],  //div
    }
  },
  computed: {
    rightConfig() {
      return this.rightConfigPointer()
    },
    stateList() {
      const obj = {};
      for (let i = 0; i < this.rightConfig.roomStateList.length; i++) {
        let state = this.rightConfig.roomStateList[i];
        obj[state.val] = state
      }
      return obj
    },
    mchildren() {
      return this.apiRoomStatus.childrenAlias
    },
    mroomNum() {
      return this.apiRoomStatus.roomNumAlias
    },
    mroomId() {
      return this.apiRoomStatus.roomIdAlias
    },
  },
  watch: {
    floorList(val) {
      this.stateRoomObj(val);
      this.$nextTick(() => {
        this.resize();
      })
    },
    loadingRoomId() {
      const diffArr = _.difference(this.chosenRoomId, this.loadingRoomId);
      this.$emit("update:chosenRoomId", diffArr)
    },
    chosenRoomId(val) {
      this.$store.commit("dormitory/chosenRoomId", val)
    },
  },
  created() {
    // Object.assign(this, this.rightConfig)
    assign(this, this.rightConfig);
  },
  methods: {
    isAllocatedRoom(reserveId){
      const opt = this.$route.query.opt;
      if (opt !== "allocation") return false;
      return reserveId === this.$route.query.reserveId
    },
    judgeLoading(roomId) {
      return this.loadingRoomId.includes(roomId)
    },
    roomCss(room) {
      return {
        chosen: this.chosenRoomId.includes(room[this.mroomId]),
        active: this.activeRoomId === room[this.mroomId]
      }
    },
    pCheckCss(floor) {
      let jud = 0;
      for (let i = 0; i < floor[this.mchildren].length; i++) {
        let room = floor[this.mchildren][i];
        let roomId = room[this.mroomId];
        for (let j = 0; j < this.chosenRoomId.length; j++) {
          let chosenId = this.chosenRoomId[j];
          if (roomId === chosenId) {
            jud += 1;
            break
          }
        }
      }
      floor.isAllCheck = jud === floor[this.mchildren].length;
        
      return floor.isAllCheck ? "icon-kongjianyixuan" : "icon-kongjianweixuan"
    },
    checkCss(room) {
      return this.chosenRoomId.includes(room[this.mroomId]) ? "icon-kongjianyixuan" : "icon-kongjianweixuan"
    },
    roomSty(room) {
      let border;
      let color = this.stateList[room.statusValue]?.color || "#000000";
      let showBorder = this.chosenRoomId.includes(room[this.mroomId]) || this.activeRoomId === room[this.mroomId];
      border = showBorder ? "4px solid " + color : "none"
        
      return {
        background: this.stateList[room.statusValue]?.bgColor || "#555555",
        color: this.stateList[room.statusValue]?.color || "#000000",
        border: border
      }
        
    },
    numSty(room) {
      return {
        background: this.stateList[room.statusValue]?.color || "#000000",
      }
    },
    checkAll(floor) {
      const chosenId = this.chosenRoomId.slice(0);
      const obj = {};
      let result = [];
        
      if (floor.isAllCheck) {
        chosenId.forEach(a => {
          obj[a] = a
        })
        floor[this.mchildren].forEach(a => {
          if (obj[a[this.mroomId]]){
            obj[a[this.mroomId]] = false
          }
        })
        for (let i in obj){
          if (obj[i]) {
            result.push(i)
          }
        }
      } else {
        const arr = floor[this.mchildren].map(a => {
          return a[this.mroomId]
        })
        chosenId.concat(arr).forEach(a => {
          obj[a] = a
        })
        result = Object.getOwnPropertyNames(obj)
      }
      this.$emit("update:chosenRoomId", result)
    },
    clickRoom(room) {
      if (this.loadingRoomId.includes(room[this.mroomId])) return;    //loading
      this.$emit("update:activeRoomId", room[this.mroomId])
      this.$emit("click-room", room)
    },
    resize() {
      const allWid = $(".room-container").width();
      const itemWid = $(".room").width() + 30;  //30 margin-left
      let rowNum = _.floor(allWid / itemWid);
      if (rowNum === this.appendOcc.length) return;   //div
      const temp = [];
      for (let i = 0; i < rowNum; i++) {
        temp.push(i);
      }
      this.appendOcc = temp;
    },
    emptyPlace() {
      this.$emit("update:activeRoomId", "")
    },
    stateRoomObj(val) {
      let obj = {};
      let mid = this.mroomId;
      let mchildren = this.mchildren;
      for (let i = 0; i < val.length; i++) {
        let floor = val[i];
        for (let j = 0; j < floor[mchildren].length; j++) {
          let room = floor[mchildren][j];
          obj[room[mid]] = room
        }
      }
      this.$store.commit("dormitory/roomObj", obj)
    },
    checkRoom(room) {
      if (this.loadingRoomId.includes(room[this.mroomId])) return;    //loading
      if (this.multipleChoose) {
        let index = this.chosenRoomId.findIndex(a => a === room[this.mroomId]);
        if (index === -1) {
          this.chosenRoomId.push(room[this.mroomId])
        } else {
          this.chosenRoomId.splice(index, 1)
        }
      } else {
        this.$emit("update:chosenRoomId", [room[this.mroomId]])
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', _.debounce(() => {
        this.resize();
      }, 100))
    })
  }
}
</script>