<template>
  <div class="floor-container group">
    <floor-box
      :multiple-choose="false"
      :floor-list="floorList"
      :active-room-id.sync="activeRoomId"
      :loading-room-id="loadingRoomId"
      @click-room="clickRoom"
    ></floor-box>
    
    <room-history-dialog
      :visible.sync="roomStatusVisible"
      :info="roomStatusInfo"
      @dialog-close="closeDialog"
    ></room-history-dialog>
  </div>
</template>

<script>
import floorBox from "_platform/pc/dormitory/room/floorBox";
import roomHistoryDialog from "_platform/pc/dormitory/dialog/roomHistoryDialog";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {customAssign, filterOriginFloor, assign} from "_platform/pc/dormitory/func";
import {mapState} from "vuex"
  
export default {
  name: "groupRoomBox",
  inject: ['rightConfigPointer'],
  components: {
    roomHistoryDialog,
    floorBox,
  },
  props: {
    queryTime: {
      type: Object,
      default() {
        return {
          sDate: "",
          eDate: ""
        }
      }
    },
    personObj: {
      type: Object,
      default() {
        return {}
      }
    },
    personList: {
      type: Array,
      default() {
        return []
      }
    },
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      dropAble: null,
      apiTeamCheckin: null,
      apiBuildingList: null,
      originFloorList: [],
      loadingRoomId: [],    //loading
      activeRoomId: '',
      //
      roomStatusVisible: false,
      roomStatusInfo: {},
      firstQueryRoomList: true,   //
    }
  },
  computed: {
    ...mapState('dormitory', [
      "activeTeamId",
      "roomStateList"
    ]),
    activeTeam() {
      return this.personList.find(it => it.teamId === this.activeTeamId);
    },
    rightConfig() {
      return this.rightConfigPointer()
    },
    mchildren() {
      return this.apiRoomStatus.childrenAlias
    },
    roomObj() {
      let obj = {};
      let mid = this.mid;
      let mchildren = this.mchildren;
      if (!this.floorList || this.floorList.length === 0) return obj;
      for (let i = 0; i < this.floorList.length; i++) {
        let floor = this.floorList[i];
        for (let j = 0; j < floor[mchildren].length; j++) {
          let room = floor[mchildren][j];
          if (obj[room[mid]]) {
            dsf.layer.alert("roomId")
            return
          }
          obj[room[mid]] = room
        }
      }
      return obj
    },
    mid() {
      return this.apiRoomStatus.roomIdAlias;
    },
    mnum() {
      return this.apiRoomStatus.roomNumAlias;
    },
    floorList() {
      return filterOriginFloor(this.originFloorList, this.roomStateList)
    },
  },
  watch: {
    floorList() {
      this.$nextTick(() => {
        this.createInteraction()
      })
    },
    activeTeamId() {
      this.queryRoomList()
    },
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);

    const currentTime = new Date().Format("yyyy-MM-dd");
      
    let start = dsf.url.queryString("sDate");
    let end = dsf.url.queryString("eDate");
    let sDate = start ? decodeURIComponent(start) : (currentTime + " 00:00:00");
    let eDate = end ? decodeURIComponent(end) : (currentTime + " 23:59:59");
    this.startTime = sDate;
    this.endTime = eDate;
  },
  mounted() {
    eventBus.on(eventBusEvent.queryRoomList, floor => {
      this.currentFloor = floor;
      // activeTeamId 
      if (!this.firstQueryRoomList){
        this.queryRoomList()
      }
    })
  },
  methods: {
    clickRoom(room) {
      this.roomStatusInfo = {
        roomId: room[this.mid],
        roomName: room[this.mnum],
        emptyBedNum: room.beds,
        ...this.queryTime
      }
        
      const team = getTeam.call(this);
      if (team) {
        this.roomStatusInfo.sDate = team.sDate;
        this.roomStatusInfo.eDate = team.eDate;
      }
        
      this.roomStatusVisible = true;
    },
    queryRoomList() {
      const team = getTeam.call(this);
      let q = {
        buildingId: this.currentFloor?.buildingId,
        type: this.currentFloor?.typeValue,
        sDate: team ? team.sDate : this.startTime,
        eDate: team ? team.eDate : this.endTime
      };
      if (_.isFunction(this.apiRoomStatus.query)) {
        let add = this.apiRoomStatus.query();
        q = customAssign(q, add);
      }
      q = dsf.mix(q, {
        reserveId: this.activeTeam?.reserveId
      });
      this.$http.get(this.apiRoomStatus.url, q)
        .done(res => {
          this.originFloorList = res.success ? res.data : [];
        })
        .always(() => {
          this.firstQueryRoomList = false;
        })
    },
    drop2room(person, room) {
      const roomId = room[this.mid];
      this.loadingRoomId.push(roomId);
        
      const q = {
        personId: person.personId,
        personName: person.personName,
        sexValue: person.sexValue,
        sexText: person.sexText,
        reserveId: person.reserveId,
        roomId: roomId
      }
      this.$http.post(this.apiTeamCheckin.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      }).done(res => {
        if (!res.success) {
          dsf.layer.message(res.message, false);
          return
        }
        dsf.layer.message("");
        eventBus.emit(eventBusEvent.refreshGroupList);
        this.queryRoomList();
      })
        .error(err => {
          dsf.layer.message("", false)
        })
        .always(() => {
          let index = this.loadingRoomId.findIndex(a => a === roomId);
          this.loadingRoomId.splice(index, 1);
        })
    },
    createInteraction() {
      const self = this;
      $(".floor-container .room").droppable({
        addClasses: false,
        tolerance: "pointer",
        over(evt, ui) {
          console.log("droppable over")
          const seatDOM = $(evt.target);
          seatDOM.css({"box-shadow": "0 0 5px inset #409eff"})
        },
        drop(evt, ui) {
          const seatDOM = $(evt.target);
          seatDOM.css("box-shadow", "none");
          const seatId = seatDOM.data("id");
          const personId = ui.helper.data("id");
          //loading
          if (self.loadingRoomId.includes(seatId)) return;
            
          const seat = self.roomObj[seatId];
          const person = self.personObj[personId];
            
            
          let isError = false; //
          if (_.isFunction(self.dropAble)) {
            isError = self.dropAble(person, seat);
          }
            
          if (isError) {
            const temp = {
              type: "warning",
            }
            dsf.layer.confirm(Object.assign(isError, temp))
            return
          }
            
          self.drop2room(person, seat);
        },
        out(evt, ui) {
          const seatDOM = $(evt.target);
          seatDOM.css("box-shadow", "none");
        }
      })
        
    },
    closeDialog(){
      this.activeRoomId= ''
      this.queryRoomList()
    },
  }
}
  
function getTeam() {
  if (!this.activeTeamId) return false;
  return this.personList.find(a => a.teamId === this.activeTeamId)
}
  
</script>