<template>
  <el-dialog :visible.sync="visible" width="40%" custom-class="dsf-dormitory-dialog" :before-close="closeDialog" :modal="false" @opened="queryRoomOccupyInfo">
    <template #title>
      <div class="plain-title">
        <span>{{info.roomName}}</span>
        <span>{{info.emptyBedNum}}</span>
      </div>
    </template>
    <div v-loading="loadingOccupyInfo" class="list-box">
      <div v-for="(item, it) in list" :key="it" class="item">
        <i class="ico-point"></i>
        <p>{{item.timeStr}}</p>
        <ul>
          <li v-for="(one, o) in item.person" :key="one.relId">
            <p>
              {{one.typeStr}}{{one.personName}}[ {{one.sexText}} ]
            </p>
            <p>
              {{one.phone}}
            </p>
            <p style="text-align: right;padding-right: 10px">
              <span v-if="opt === 'resetGroup'" @click="resetGroup(one)"></span>
              <span @click="go2Info(one)"></span>
            </p>
          </li>
          <li v-for="(empty, em) in item.release" :key="em">
            <p></p>
          </li>
        </ul>
      </div>
    </div>

    <template #footer>
      <div class="dialog-footer">
        <el-button @click="closeDialog"> </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import {assign} from "_platform/pc/dormitory/func";

export default {
  name: "roomHistoryDialog",
  inject: ["dormitoryRoot", "rightConfigPointer"],
  props: {
    info: {
      type: Object,
      default() {
        return {
          roomName: "",
          emptyBedNum: "",
          roomId: "",
          sDate: "",
          eDate: ""
        };
      }
    },
    visible: {
      type: Boolean,
      default: false,
      required: true
    }
  },
  computed: {
    rightConfig() {
      return this.rightConfigPointer();
    },
    opt() {
      return this.$route.query.opt;
    }
  },
  data() {
    return {
      //
      apiRoomOccupyInfo: null,
      roomStatusDetailClick: null,
      apiResetPerson: null,

      //
      loadingOccupyInfo: false,
      list: []
    };
  },
  /*watch: {
      "info.roomId"() {
        this.queryRoomOccupyInfo()
      },
    },*/
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig);
  },
  methods: {
    closeDialog() {
      this.$emit("dialog-close");
      this.$emit("update:visible", false);
    },
    queryRoomOccupyInfo() {
      this.loadingOccupyInfo = true;
      const q = {
        roomId: this.info.roomId,
        sDate: this.info.sDate,
        eDate: this.info.eDate
      };
      this.$http
        .get(this.apiRoomOccupyInfo.url, q)
        .done((res) => {
          let data = res.data;
          if (_.isFunction(this.apiRoomOccupyInfo.callback)) {
            data = this.apiRoomOccupyInfo.callback(data);
          }
          this.list = data;
        })
        .always(() => {
          this.loadingOccupyInfo = false;
        });
    },
    go2Info(one) {
      if (!_.isFunction(this.roomStatusDetailClick)) return;
      this.roomStatusDetailClick.call(this, one);
    },
    resetGroup(one) {
      const q = {
        personId: one.personId
      };
      this.$http
        .post(this.apiResetPerson.url, q)
        .done((res) => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return;
          }
          this.queryRoomOccupyInfo();
          this.dormitoryRoot.reset();
          dsf.layer.message("");
        })
        .error((err) => {
          dsf.layer.message("", false);
        });
    }
  }
};
</script>