<template>
  <div>
    <el-dialog
      :visible.sync="visible"
      width="40%"
      :before-close="beforeClose"
      :modal="false"
      :append-to-body="true"
      :lock-scroll="true"
      custom-class="dsf-dormitory-dialog"
      @close="guestsClose"
    >
      <template #title>
        <div class="plain-title">
          <span>{{info.roomName}}</span>
          <span>{{info.emptyBedNum}}</span>
        </div>
      </template>
      <div v-loading="loadingOccupyInfo" class="list-box">
        <div v-for="(item, it) in list" :key="it" class="item">
          <i class="ico-point"></i>
          <p>{{item.timeStr}}</p>
          <ul>
            <li v-for="(one, o) in item.person" :key="one.relId">
              <p>
                {{one.typeStr}}{{one.personName}}[ {{one.sexText}} ]
              </p>
              <p>
                {{one.phone}}
              </p>
              <p>
                <template v-if="one.type === 1">
                  <span v-if="!isReserveModule" @click="renewal(one)"></span>
                  <span @click="checkOut(one)"></span>
                  <span v-if="!one.past" @click="changeRoom(one)"></span>
                </template>
                <template v-if="one.type === 0 && !isReserveModule">
                  <span @click="checkIn(one)"></span>
                  <span @click="cancelReserve(one)"></span>
                </template>
                <template v-if="one.type === 2 && !isReserveModule">
                  <span @click="reserve"></span>
                  <span @click="checkIn"></span>
                </template>
              </p>
            </li>
            <li v-for="(empty, em) in item.release" :key="em">
              <p></p>
              <p></p>
              <p v-if="info.statusValue !== 0 && !isReserveModule">
                <span @click="reserve(item)"></span>
                <span @click="checkIn(item)"></span>
              </p>
              <p v-else>
                <span class="warning"></span>
              </p>
            </li>
          </ul>
        </div>
      </div>
      <template #footer>
        <div class="dialog-footer">
          <el-button @click="closeDialog"> </el-button>
        </div>
      </template>
    </el-dialog>
    <!---->
    <action-dialog
      :visible.sync="checkInVisible"
      title=""
      @save="checkInSave"
      @dialog-close="closeFormDialog('checkInForm')"
    >
      <template #body>
        <el-form ref="checkInForm" :model="checkInForm" :rules="formRules" label-width="100px">
          <el-form-item label="" prop="personName">
            <el-input v-model="checkInForm.personName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="idCard">
            <el-input v-model="checkInForm.idCard"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="checkInForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div class="date-box">
              <span>{{checkInForm.sDate}}</span>
              <span class="text-divider"></span>
              <span>{{checkInForm.eDate}}</span>
            </div>
          </el-form-item>
        </el-form>
      </template>
    </action-dialog>
    
    <!---->
    <action-dialog
      :visible.sync="reserveVisible"
      title=""
      @save="reserveSave"
      @dialog-close="closeFormDialog('reserveForm')"
    >
      <template #body>
        <el-form ref="reserveForm" :model="reserveForm" :rules="formRules" label-width="100px">
          <el-form-item label="" prop="personName">
            <el-input v-model="reserveForm.personName"></el-input>
          </el-form-item>
          <el-form-item label="" prop="idCard">
            <el-input v-model="reserveForm.idCard"></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="reserveForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div class="date-box">
              <span>{{reserveForm.sDate}}</span>
              <span class="text-divider"></span>
              <span>{{reserveForm.eDate}}</span>
            </div>
          </el-form-item>
        </el-form>
      </template>
    </action-dialog>
    
    <!---->
    <action-dialog
      :visible.sync="renewalVisible"
      title=""
      @save="renewalSave"
      @dialog-close="closeFormDialog('renewalForm')"
    >
      <template #body>
        <el-form ref="renewalForm" :model="renewalForm" :rules="formRules" label-width="120px">
          <el-form-item label="" prop="personName">
            <el-input v-model="renewalForm.personName" :readonly="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="idCard">
            <el-input v-model="renewalForm.idCard" :readonly="true"></el-input>
          </el-form-item>
          <el-form-item class="justify" label="">
            <time-range :form="renewalForm" :readonly-arr="[true, true]"></time-range>
          </el-form-item>
          <el-form-item class="justify" label="" prop="eDate">
            <time-range :form="newTimeForm" :readonly-arr="[true, false]"></time-range>
          </el-form-item>
        </el-form>
      </template>
    </action-dialog>
    
    <!---->
    <action-dialog
      :visible.sync="changeRoomVisible"
      title=""
      width="80%"
      :show-close="false"
      top="8vh"
      @save="changeRoomSave"
    >
      <template #topTool>
        <el-form :inline="true">
          <el-form-item :label="inlineForm[0].label">
            <el-select v-model="inlineForm[0].value" @change="changeSelect(0)" placeholder="">
              <el-option
                v-for="(item, it) in treeList"
                :key="item[mid]"
                :value="item[mid]"
                :label="item[mlabel]"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="inlineForm[1].label">
            <el-select v-model="inlineForm[1].value" @change="changeSelect(1)" placeholder="">
              <el-option
                v-for="(item, it) in selectList1"
                :key="item[mid]"
                :value="item[mid]"
                :label="item[mlabel]"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item :label="inlineForm[2].label">
            <el-select v-model="inlineForm[2].value" @change="changeSelect(2)" placeholder="">
              <el-option
                v-for="(item, it) in selectList2"
                :key="item[mid]"
                :value="item[mid]"
                :label="item[mlabel]"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="queryRoomName"></el-input>
          </el-form-item>
          <el-form-item>
            <dsf-button @click="reloadQuery"> </dsf-button>
            <dsf-button icon="sousuo" @click="queryRoomList"></dsf-button>
          </el-form-item>
        </el-form>
        <div v-show="false" class="time-item">
          <label></label>
          <time-range :form="newTimeForm"></time-range>
        </div>
      </template>
      <template #floorBox>
        <floor-box
          :floor-list="floorList"
          :active-room-id.sync="activeRoomId"
        ></floor-box>
      </template>
    </action-dialog>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
import actionDialog from "_platform/pc/dormitory/dialog/actionDialog";
import floorBox from "_platform/pc/dormitory/room/floorBox";
import timeRange from "_platform/pc/dormitory/dialog/timeRange";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {assign} from "_platform/pc/dormitory/func";
  
export default {
  name: "guestsOptDialog",
  inject: ['leftConfigPointer', 'rightConfigPointer'],
  props: {
    info: {
      type: Object,
      default() {
        return {
          roomName: "",
          emptyBedNum: "",
          roomId: "",
          sDate: "",
          eDate: "",
          statusValue: null
        }
      }
    },
    visible: {
      type: Boolean,
      default: false,
      required: true
    },
    room: {
      type: Object,
    },
    treeList: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  components: {
    actionDialog,
    floorBox,
    timeRange
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      apiRoomOccupyInfo: null,
      apiCheckIn: null,
      apiCheckOut: null,
      apiRenewal: null,
      apiReserve: null,
      apiCancelReserve: null,
      apiChangeRoom: null,
        
      //
      checkInVisible: false,
      checkInForm: {},
      reserveVisible: false,
      reserveForm: {},
      renewalVisible: false,
      renewalForm: {},
      //
      formRules: {
        personName: [
          {required: true, message: ""}
        ],
        idCard: [
          {
            required: true,
            validator: (rule, value, callback) => {
              if (!value) {
                return callback(new Error(''));
              }
              if (!dsf.validate('isIDCard', value)) {
                return callback(new Error(''));
              }
              callback();
            },
            trigger: ['blur']
          }
        ],
        phone: [
          {
            required: true,
            validator: (rule, value, callback) => {
              if (!value) {
                return callback(new Error(''));
              }
              if (!dsf.validate('isMobile', value)) {
                return callback(new Error(''));
              }
              callback()
            },
            trigger: ['blur']
          },
          {}
        ]
      },
        
      newTimeForm: {},
        
      changeRoomVisible: false,
      queryInfo: {},
        
      list: [],
      isDataChanged: false,   //
      selectList1: [],
      selectList2: [],
      inlineForm: [],
      originInlineForm: [],   //
      queryRoomName: "",    //
      currentSelectFloor: null,    //
      floorList: [],
      activeRoomId: "",   //
      loadingOccupyInfo: true,
    }
  },
  computed: {
    ...mapGetters('dormitory', [
      'roomStateDict'
    ]),
    leftConfig() {
      return this.leftConfigPointer()
    },
    rightConfig() {
      return this.rightConfigPointer()
    },
    mid() {
      return this.leftConfig.leftProps.id
    },
    mlabel() {
      return this.leftConfig.leftProps.label
    },
    mtype() {
      return this.leftConfig.leftProps.type
    },
    mroomId() {
      return this.apiRoomStatus.roomIdAlias
    },
    // roomreserveId
    isReserveModule() {
      if (dsf.config.setting_sgcp_reserve == 1) {
        return this.room.reserveId;
      }
      return false;
    }
  },
  watch: {
    "visible"(val) {
      if (val) this.queryRoomOccupyInfo()
    },
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig);
    this.inlineForm = this.leftConfig.inlineForm;
    this.originInlineForm = _.cloneDeep(this.leftConfig.inlineForm);
  },
  methods: {
    changeSelect(num) {
      const self = this;
      let a = num;
      //
      while (self["selectList" + (a + 1)]) {
        self["selectList" + (a + 1)] = []
        self.inlineForm[a + 1].value = "";
        a++
      }
        
      //
      const s = {
        id: self.inlineForm[num].value,
        callback(result) {
          self.currentSelectFloor = result
          self.queryRoomList()
        }
      }
      eventBus.emit(eventBusEvent.treeCurrent, s);
        
      //
      const q = {
        id: self.inlineForm[num].value,
        callback(result) {
          self["selectList" + (num + 1)] = result
        }
      }
      eventBus.emit(eventBusEvent.treeNextLevel, q);
    },
    closeDialog() {
      this.$emit("update:visible", false);
    },
    beforeClose() {
      this.closeDialog();
      this.room.chosen = false;
    },
    // 
    renewal(one) {
      const time = new Date(one.edate).getTime() + 1000;
      const date = new Date(time);
      this.renewalForm = {
        personName: one.personName,
        phone: one.phone,
        idCard: one.idCard,
        occupyId: one.occupyId,
        sDate: one.sdate,
        eDate: one.edate,
      }
      this.newTimeForm = {
        sDate: date,
        eDate: "",
      }
      this.renewalVisible = true;
    },
    renewalSave(btnLoading) {
      this.$refs.renewalForm.validate().then(() => {
        if (!this.newTimeForm.eDate) {
          dsf.layer.message("", false);
          return
        }
        btnLoading.save = true
        const q = {
          occupyId: this.renewalForm.occupyId,
          lastDate: this.renewalForm.eDate,
          curDate: this.newTimeForm.eDate,
          roomId: this.info.roomId
        };
        this.$http.post(this.apiRenewal.url, JSON.stringify(q), {
          headers: {
            "Content-Type": "application/json; charset=utf-8"
          }
        })
          .done(res => {
            if (!res.success) {
              dsf.layer.message(res.message, false);
              return
            }
              
            this.isDataChanged = true;
            this.renewalVisible = false;
            dsf.layer.message("");
            this.queryRoomOccupyInfo();
          })
          .error(err => {
            dsf.layer.message("", false)
          })
          .always(() => {
            setTimeout(() => {
              btnLoading.save = false
            }, 700)
          })
      }).catch(err => {
        //
      })
    },
    // 
    checkIn(one) {
      if (isPastDue(one, 'edate')) {
        dsf.layer.message("", false)
        return
      }
        
      this.checkInForm = {
        personName: one?.personName,
        idCard: one?.idCard,
        phone: one?.phone,
        sDate: one?.sdate?.replace(/:\d\d$/, ""),
        eDate: one?.edate?.replace(/:\d\d$/, ""),
        reserveId: one?.relId,
        roomId: this.info.roomId
      }
      this.checkInVisible = true;
    },
    checkInSave(btnLoading) {
      this.$refs.checkInForm.validate().then(() => {
        btnLoading.save = true
        this.$http.post(this.apiCheckIn.url, JSON.stringify(this.checkInForm), {
          headers: {
            "Content-Type": "application/json; charset=utf-8"
          }
        })
          .done(res => {
            if (!res.success) {
              dsf.layer.message(res.message, false);
              return
            }
              
            this.isDataChanged = true;
            this.checkInVisible = false;
            dsf.layer.message("");
            this.queryRoomOccupyInfo();
          })
          .error(err => {
            dsf.layer.message("", false);
          })
          .always(() => {
            setTimeout(()=>{
              btnLoading.save = false
            }, 700)
          })
      }).catch(err => {
        //
      })
    },
    // 
    checkOut(one) {
      dsf.layer.confirm({
        message: "",
        type: "warning",
        customClass: "el-message-box-center"
      })
        .then(() => {
          const q = {
            roomId: this.info.roomId,
            occupyId: one.relId
          }
          this.$http.post(this.apiCheckOut.url, JSON.stringify(q), {
            headers: {
              "Content-Type": "application/json; charset=utf-8"
            }
          })
            .done(res => {
              if (!res.success) {
                dsf.layer.message(res.message, false);
                return
              }
              this.isDataChanged = true;
              dsf.layer.message("");
              this.queryRoomOccupyInfo();
            })
            .error(err => {
              dsf.layer.message("", false)
            })
        })
        .catch(err => {
          //
        })
        
    },
    // 
    reserve(one) {
      if (isPastDue(one, 'edate')) {
        dsf.layer.message("", false);
        return
      }
      this.reserveForm = {
        personName: "",
        idCard: "",
        phone: "",
        sexValue: one.sexValue,
        sexText: one.sexText,
        sDate: one?.sdate?.replace(/:\d\d$/, ""),
        eDate: one?.edate?.replace(/:\d\d$/, ""),
        reserveId: one?.relId,
        roomId: this.info.roomId
      }
      this.reserveVisible = true;
    },
    reserveSave(btnLoading) {
      this.$refs.reserveForm.validate().then(() => {
        btnLoading.save = true
        this.$http.post(this.apiReserve.url, JSON.stringify(this.reserveForm), {
          headers: {
            "Content-Type": "application/json; charset=utf-8"
          }
        })
          .done(res => {
            if (!res.success) {
              dsf.layer.message(res.message, false);
              return
            }
              
            this.isDataChanged = true;
            this.reserveVisible = false;
            dsf.layer.message("");
            this.queryRoomOccupyInfo();
          })
          .error(err => {
            dsf.layer.message("", false);
          })
          .always(() => {
            setTimeout(() => {
              btnLoading.save = false
            }, 700)
          })
      }).catch(err => {
        //
      })
    },
    // todo 
    cancelReserve(one) {
      if (isPastDue(one, 'sdate')) {
        dsf.layer.message("", false);
        return
      }
      dsf.layer.message("", false);
      // let loading = dsf.layer.loading();
      // this.$http.post(this.apiCancelReserve.url, JSON.stringify({
      //
      // }), {
      //   headers: {
      //     "Content-Type": "application/json; charset=utf-8"
      //   }
      // })
      //   .done(({success, message}) => {
      //     if (!success) {
      //       dsf.layer.message(message, false);
      //     }
      //   })
      //   .error(err => {
      //     dsf.layer.message("", false);
      //   })
      //   .always(() => {
      //     dsf.layer.closeLoading(loading);
      //   })
    },
    reloadQuery() {
      this.inlineForm = _.cloneDeep(this.originInlineForm);
      this.selectList1 = [];
      this.selectList2 = [];
      this.queryRoomName = ""
  
      //
      this.inlineForm[0].value = this.treeList[0][this.mid]
      this.changeSelect(0)
    },
    // 
    changeRoom(one) {
      const currentTime = new Date().Format("yyyy-MM-dd hh:mm:ss");
      this.newTimeForm = {
        sDate: currentTime,
        eDate: one.edate,
        occupyId: one.occupyId
      }
      this.floorList = [];
      this.activeRoomId = "";
      this.changeRoomVisible = true;
        
      //
      this.inlineForm[0].value = this.treeList[0][this.mid]
      this.changeSelect(0)
    },
    queryRoomOccupyInfo() {
      this.loadingOccupyInfo = true;
      const q = {
        roomId: this.info.roomId,
        sDate: this.info.sDate,
        eDate: this.info.eDate
      }
      this.$http.get(this.apiRoomOccupyInfo.url, q)
        .done(res => {
          let data = res.data;
          if (_.isFunction(this.apiRoomOccupyInfo.callback)) {
            data = this.apiRoomOccupyInfo.callback(data);
          }
          this.list = data;
        })
        .always(() => {
          this.loadingOccupyInfo = false
        })
    },
    guestsClose() {
      this.$emit("close-dialog", this.isDataChanged);
    },
    queryRoomList() {
      let q = {
        buildingId: this.currentSelectFloor[this.mid],
        type: this.currentSelectFloor[this.mtype],
        sDate: this.newTimeForm.sDate,
        eDate: this.newTimeForm.eDate,
        name: this.queryRoomName
      }
        
      this.$http.get(this.apiRoomStatus.url, q).done(res => {
        this.activeRoomId = "";
          
        if (!res.success) {
          this.floorList = []
          return
        }
          
        ///
        this.floorList = res.data.map(floor => {
          floor.roomDtos = floor.roomDtos.filter(room =>
            room.statusValue === this.roomStateDict.normal || room.statusValue === this.roomStateDict.some
          )
          return floor
        })
      })
    },
    changeRoomSave(btnLoading) {
      if (!this.activeRoomId) {
        dsf.layer.message("", false);
        return
      }
      btnLoading.save = true
      const q = {
        occupyId: this.newTimeForm.occupyId,
        roomId: this.activeRoomId
      }
      this.$http.post(this.apiChangeRoom.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return
          }
            
          this.isDataChanged = true;
          this.changeRoomVisible = false;
          dsf.layer.message("");
          this.queryRoomOccupyInfo();
        })
        .error(err => {
          dsf.layer.message("", false);
        })
        .always(() => {
          setTimeout(()=>{
            btnLoading.save = false
          }, 700)
        })
    },
    closeFormDialog(ref) {
      this.$refs[ref].clearValidate()
    },
  },
  mounted() {
  }
}

// 
function isPastDue(one, key = 'edate') {
  if (!one) return false;
  const now = new Date().Format("yyyy-MM-dd hh:mm:00");
  const edate = one[key].replace(/:\d\d$/g, "") + ":00";

  const now_t = new Date(now).getTime();
  const edate_t = new Date(edate).getTime();
  return now_t > edate_t
}
</script>