<template>
  <div class="floor-container">
    <floor-box
      :floor-list="floorList"
      :active-room-id.sync="activeRoomId"
      @click-room="clickRoom"
    ></floor-box>
    <guests-opt-dialog
      :visible.sync="guestsVisible"
      :room="currentRoom"
      :info="roomStatusInfo"
      :tree-list="treeList"
      @close-dialog="onCloseDialog"
    ></guests-opt-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import guestsOptDialog from "_platform/pc/dormitory/dialog/guestsOptDialog";
import floorBox from "_platform/pc/dormitory/room/floorBox";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {customAssign, filterOriginFloor, assign} from "_platform/pc/dormitory/func";

export default {
  name: "roomBox",
  inject: ['rightConfigPointer'],
  props: {
    queryTime: {
      type: Object,
      default(){
        return {
          sDate: "",
          eDate: ""
        }
      }
    },
    treeList:{
      type: Array,
    }
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      //
      originFloorList: [],

      guestsVisible: false,
      currentRoom: null,
      roomStatusInfo: {},
      activeRoomId: "",   //roomId
    }
  },
  computed: {
    rightConfig(){
      return this.rightConfigPointer()
    },
    mchildren() {
      return this.apiRoomStatus.childrenAlias
    },
    mroomNum() {
      return this.apiRoomStatus.roomNumAlias
    },
    mroomId() {
      return this.apiRoomStatus.roomIdAlias
    },
    floorList() {
      return filterOriginFloor(this.originFloorList, this.roomStateList)
    },
    ...mapState('dormitory', [
      "currentFloor",
      "roomStateList"
    ])
  },
  components: {
    guestsOptDialog,
    floorBox
  },
  watch: {
    currentFloor() {
      this.queryRoomList()
    },
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
  },
  mounted() {
    eventBus.on(eventBusEvent.queryRoomList, () => {
      this.queryRoomList()
    })
  },
    
  methods: {
    clickRoom(item) {
      this.currentRoom = item;
      this.popGuests()
    },
    popGuests() {
      this.roomStatusInfo = {
        roomId: this.currentRoom[this.mroomId],
        roomName: this.currentRoom[this.mroomNum],
        emptyBedNum: this.currentRoom.beds,
        statusValue: this.currentRoom.statusValue,    //
        ...this.queryTime
      }
      this.guestsVisible = true;
    },
    queryRoomList() {
      let q = {
        ...this.currentFloor,
      };
  
      if (_.isFunction(this.apiRoomStatus.query)){
        let add = this.apiRoomStatus.query();
        q = customAssign(q, add);
      }
      q = Object.assign({}, q, this.queryTime);
        
      this.$http.get(this.apiRoomStatus.url, q).done(res => {
        this.originFloorList = res.success? res.data: [];
        this.activeRoomId = "";
      })
    },
    onCloseDialog(needReload){
      this.chosenRoomId = "";
      needReload && this.queryRoomList();
    }
  }
}
</script>