<template>
  <div class="floor-container group">
    <floor-box
      :multiple-choose="true"
      :floor-list="floorList"
      :chosen-room-id.sync="chosenRoomId"
      :active-room-id.sync="activeRoomId"
      :loading-room-id="loadingRoomId"
      @click-room="clickRoom"
    ></floor-box>
    
    <action-dialog title="" :visible.sync="autoDialogVisible" top="20px" width="60%" @save="saveAuto">
      <template #body>
        <el-form :model="form" label-width="100px" class="auto-checkin-form">
          <el-form-item label="">
            <el-select
              v-model="form.reserveIds"
              multiple
              placeholder=""
              @change="changeTeamSelect"
              @visible-change="selectExpand">
              <el-option
                v-for="team in personList"
                :label="team.teamName"
                :value="team.reserveId"
                :key="team.reserveId"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox-group v-model="form.buildingIds" @change="changeBuildCheckGroup">
              <div v-for="(item, it) in buildList" :key="it" class="checkbox-warp">
                <el-checkbox :label="item.buildingId">{{item.name}}</el-checkbox>
              </div>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="">
            <div class="floor-checkbox-warp" v-if="rowList.length > 0">
              <div v-for="(row, r) in rowList" :key="r">
                <label class="floor-name"><span>{{row.floorName}}</span></label>
                <el-checkbox-group v-model="form.floorIds">
                  <div v-for="(item, it) in row.floorItems" :key="it" class="checkbox-warp">
                    <el-checkbox :label="item.buildingId">{{item.name}}</el-checkbox>
                  </div>
                </el-checkbox-group>
              </div>
            </div>
            
            <div v-else class="no-check-group"></div>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox-group v-model="form.types">
              <div v-for="(item, it) in roomTypesByAuto" :key="it" class="checkbox-warp">
                <el-checkbox :label="item.val">{{item.name}}</el-checkbox>
              </div>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="">
            <el-checkbox-group :value="form.rules">
              <div v-for="(item, it) in rulesByAuto" :key="it" class="checkbox-warp">
                <el-checkbox
                  :label="item.val"
                  @change="status => changeRuleCheck(status, item)"
                  :disabled="item.disabled"
                  :checked="item.default"
                >
                  {{item.name}}
                </el-checkbox>
              </div>
            </el-checkbox-group>
          </el-form-item>
          <el-form-item label="">
            <div v-if="timeForm.sDate" class="date-box">
              <span>{{timeForm.sDate}}</span>
              <span class="text-divider"></span>
              <span>{{timeForm.eDate}}</span>
            </div>
            <span v-else> -- </span>
          </el-form-item>
        </el-form>
      </template>
    </action-dialog>
    <room-history-dialog
      :visible.sync="roomStatusVisible"
      :info="roomStatusInfo"
      @dialog-close="activeRoomId= ''"
    ></room-history-dialog>
  </div>
</template>

<script>
import floorBox from "_platform/pc/dormitory/room/floorBox";
import actionDialog from "_platform/pc/dormitory/dialog/actionDialog";
import roomHistoryDialog from "_platform/pc/dormitory/dialog/roomHistoryDialog";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {customAssign, filterOriginFloor, assign} from "_platform/pc/dormitory/func";
import {mapState} from "vuex"
  
export default {
  name: "groupRoomBox",
  inject: ['rightConfigPointer'],
  components: {
    roomHistoryDialog,
    floorBox,
    actionDialog,
  },
  props: {
    queryTime: {
      type: Object,
      default() {
        return {
          sDate: "",
          eDate: ""
        }
      }
    },
    personObj: {
      type: Object,
      default() {
        return {}
      }
    },
    personList: {
      type: Array,
      default() {
        return []
      }
    },
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      dropAble: null,
      apiTeamCheckin: null,
      apiTeamCheckOut: null,
      apiRestoreSelectRoom: null,
      apiBuildingList: null,
      apiAutoCheckIn: null,
      apiReserveCancel: null,
      roomTypesByAuto: null,    //
      rulesByAuto: null,        //
        
      originFloorList: [],
      chosenRoomId: [],     //roomId
      activeRoomId: "",     //roomId
      currentFloor: null,   //
      loadingRoomId: [],    //loading
      autoDialogVisible: false,
      form: {},
      buildList: [],    //
      rowList: [],    //
      timeForm: {},    //
        
      //
      roomStatusVisible: false,
      roomStatusInfo: {},
        
      firstQueryRoomList: true,   //
    }
  },
  computed: {
    ...mapState('dormitory', [
      "activeTeamId",
      "roomStateList"
    ]),
    rightConfig() {
      return this.rightConfigPointer()
    },
    mchildren() {
      return this.apiRoomStatus.childrenAlias
    },
    roomObj() {
      let obj = {};
      let mid = this.mid;
      let mchildren = this.mchildren;
      if (!this.floorList || this.floorList.length === 0) return obj;
      for (let i = 0; i < this.floorList.length; i++) {
        let floor = this.floorList[i];
        for (let j = 0; j < floor[mchildren].length; j++) {
          let room = floor[mchildren][j];
          if (obj[room[mid]]) {
            dsf.layer.alert("roomId")
            return
          }
          obj[room[mid]] = room
        }
      }
      return obj
    },
    mid() {
      return this.apiRoomStatus.roomIdAlias;
    },
    mnum() {
      return this.apiRoomStatus.roomNumAlias;
    },
    floorList() {
      return filterOriginFloor(this.originFloorList, this.roomStateList)
    },
  },
  watch: {
    floorList() {
      this.$nextTick(() => {
        this.createInteraction()
      })
    },
    activeTeamId() {
      this.queryRoomList()
    },
    autoDialogVisible(val) {
      if (!val) return;
      $(".el-select-dropdown__list").children("li.hover").removeClass("hover")
    },
    roomStateList(){
      this.chosenRoomId = []
    }
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
    const currentTime = new Date().Format("yyyy-MM-dd");
      
    let start = dsf.url.queryString("sDate");
    let end = dsf.url.queryString("eDate");
    let sDate = start ? decodeURIComponent(start) : (currentTime + " 00:00:00");
    let eDate = end ? decodeURIComponent(end) : (currentTime + " 23:59:59");
    this.startTime = sDate;
    this.endTime = eDate;
  },
  mounted() {
    this.GetAllBuildList()
    eventBus.on(eventBusEvent.queryRoomList, floor => {
      this.currentFloor = floor;
      // activeTeamId 
      if (!this.firstQueryRoomList){
        this.queryRoomList()
      }
    })
    eventBus.on(eventBusEvent.chosenRoomCheckOut, btnLoading => {
      this.chosenRoomCheckOut(btnLoading);
    })
    eventBus.on(eventBusEvent.reserveRoomCancel, btnLoading => {
      this.reserveRoomCancel(btnLoading);
    })
    eventBus.on(eventBusEvent.restoreSelectRoom, btnLoading => {
      this.restoreSelectRoom(btnLoading);
    })
    eventBus.on(eventBusEvent.restoreAllRoom, btnLoading => {
      this.restoreAllRoom(btnLoading)
    })
    eventBus.on(eventBusEvent.autoCheckIn, () => {
      if (!_.isArray(this.floorList) || this.floorList.length === 0){
        dsf.layer.message("", false);
        return
      }
        
      this.rowList = [];
        
      let reserveIds, team;
      if (_.isArray(this.personList)) {
        team = this.activeTeamId ? getTeam.call(this): this.personList[0];
        reserveIds = [team.reserveId];
        this.timeForm = {
          sDate: team.sDate.replace(/:\d\d$/, ""),
          eDate: team.eDate.replace(/:\d\d$/, "")
        }
          
      } else {
        reserveIds = []
        this.timeForm = {
          sDate: "",
          eDate: ""
        }
      }
        
      //
      const defaultChosenTypes = [];
      this.rulesByAuto.forEach(a => {
        if (a.default === "checked") defaultChosenTypes.push(a.val)
      });
        
      this.form = {
        reserveIds: reserveIds,
        buildingIds: [],
        floorIds: [],
        types: [],
        rules: defaultChosenTypes
      }
      this.autoDialogVisible = true
    })
  },
  methods: {
    reserveRoomCancel(btnLoading) {
      const team = getTeam.call(this);
      if (!this.chosenRoomId || this.chosenRoomId.length === 0) {
        dsf.layer.message("", false);
        return false
      }
      if (!team) {
        dsf.layer.message("", false)
        return;
      }
        
      btnLoading.cancel = true
      this.loadingRoomId = this.chosenRoomId;
      const q = {
        roomIds: this.chosenRoomId,
        reserveId: team.reserveId
      }
      this.$http.post(this.apiReserveCancel.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return
          }
          dsf.layer.message("");
          this.queryRoomList();
          eventBus.emit(eventBusEvent.refreshGroupList);
        })
        .error(err => {
          dsf.layer.message("", false)
        })
        .always(() => {
          btnLoading.cancel = false;
          this.loadingRoomId = [];
        })
        
        
    },
    selectExpand(val) {
      console.log(val)
      $(".el-select-dropdown__list").children("li.hover").removeClass("hover")
    },
    clickRoom(room) {
      this.roomStatusInfo = {
        roomId: room[this.mid],
        roomName: room[this.mnum],
        emptyBedNum: room.beds,
        ...this.queryTime
      }
        
      const team = getTeam.call(this);
      if (team) {
        this.roomStatusInfo.sDate = team.sDate;
        this.roomStatusInfo.eDate = team.eDate;
      }
        
      this.roomStatusVisible = true;
    },
    queryRoomList() {
      const team = getTeam.call(this);
      let q = {
        buildingId: this.currentFloor?.buildingId,
        type: this.currentFloor?.typeValue,
        sDate: team ? team.sDate : this.startTime,
        eDate: team ? team.eDate : this.endTime
      };
      if (_.isFunction(this.apiRoomStatus.query)) {
        let add = this.apiRoomStatus.query();
        q = customAssign(q, add);
      }
        
      this.$http.get(this.apiRoomStatus.url, q)
        .done(res => {
          this.originFloorList = res.success ? res.data : [];
          this.chosenRoomId = [];
        })
        .always(() => {
          this.firstQueryRoomList = false;
        })
    },
    drop2room(person, room) {
      const roomId = room[this.mid];
      this.loadingRoomId.push(roomId);
        
      const q = {
        personId: person.personId,
        personName: person.personName,
        sexValue: person.sexValue,
        sexText: person.sexText,
        reserveId: person.reserveId,
        roomId: roomId
      }
      this.$http.post(this.apiTeamCheckin.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      }).done(res => {
        if (!res.success) {
          dsf.layer.message(res.message, false);
          return
        }
        dsf.layer.message("");
        eventBus.emit(eventBusEvent.refreshGroupList);
        this.queryRoomList();
      })
        .error(err => {
          dsf.layer.message("", false)
        })
        .always(() => {
          let index = this.loadingRoomId.findIndex(a => a === roomId);
          this.loadingRoomId.splice(index, 1);
        })
    },
    createInteraction() {
      const self = this;
      $(".floor-container .room").droppable({
        addClasses: false,
        tolerance: "pointer",
        over(evt, ui) {
          console.log("droppable over")
          const seatDOM = $(evt.target);
          seatDOM.css({"box-shadow": "0 0 5px inset #409eff"})
        },
        drop(evt, ui) {
          const seatDOM = $(evt.target);
          seatDOM.css("box-shadow", "none");
          const seatId = seatDOM.data("id");
          const personId = ui.helper.data("id");
          //loading
          if (self.loadingRoomId.includes(seatId)) return;
            
          const seat = self.roomObj[seatId];
          const person = self.personObj[personId];
            
            
          let isError = false; //
          if (_.isFunction(self.dropAble)) {
            isError = self.dropAble(person, seat);
          }
            
          if (isError) {
            const temp = {
              type: "warning",
            }
            dsf.layer.confirm(Object.assign(isError, temp))
            return
          }
            
          self.drop2room(person, seat);
        },
        out(evt, ui) {
          const seatDOM = $(evt.target);
          seatDOM.css("box-shadow", "none");
        }
      })
        
    },
    chosenRoomCheckOut(btnLoading) {
      if (!this.chosenRoomId || this.chosenRoomId.length === 0) {
        dsf.layer.message("", false);
        return false
      }
      const team = getTeam.call(this);
      if (!team) {
        dsf.layer.message("", false)
        return;
      }
      btnLoading.checkOut = true
      this.loadingRoomId = this.chosenRoomId;
      const q = {
        reserveId: team.reserveId,
        roomIds: this.chosenRoomId
      }
      this.$http.post(this.apiTeamCheckOut.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return
          }
            
          dsf.layer.message("");
          this.queryRoomList();
          eventBus.emit(eventBusEvent.refreshGroupList);
        })
        .error(err => {
          dsf.layer.message("", false)
        })
        .always(() => {
          btnLoading.checkOut = false;
          this.loadingRoomId = [];
        })
    },
    restoreSelectRoom(btnLoading) {
      if (!this.chosenRoomId || this.chosenRoomId.length === 0) {
        dsf.layer.message("", false);
        return false
      }
      const team = getTeam.call(this);
      if (!team) {
        dsf.layer.message("", false)
        return;
      }
        
      let roomMsg = ""
      this.chosenRoomId.forEach(a => {
        let roomName = this["roomObj"][a][this.mnum]
        roomMsg += `<span>${roomName}</span>`
      })
        
      const func1 = () => {
        this.loadingRoomId = this.chosenRoomId;
        btnLoading.restore = true;
        const q = {
          reserveId: team.reserveId,
          roomIds: this.chosenRoomId,
        }
        this.$http.post(this.apiRestoreSelectRoom.url, JSON.stringify(q), {
          headers: {"Content-Type": "application/json; charset=utf-8"}
        }).done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return
          }
          dsf.layer.message("");
          this.queryRoomList();
          eventBus.emit(eventBusEvent.refreshGroupList);
        }).error(err => {
          dsf.layer.message("", false)
        })
          .always(() => {
            btnLoading.restore = false;
            this.loadingRoomId = [];
          })
      }
        
      dsf.layer.confirm({
        message: `<p><b>${team.teamName}</b></p><p class="room-msg">${roomMsg}</p><p></p>`,
        customClass: "dormitory-restore-select-room-confirm"
      }).then(() => {
        func1()
      }).catch(() => {
      })
        
    },
    restoreAllRoom(btnLoading) {
      const team = getTeam.call(this);
      if (!team) {
        dsf.layer.message("", false)
        return;
      }
      dsf.layer.confirm({
        message: `<p><b>${team.teamName}</b></p><p></p>`
      }).then(() => {
        btnLoading.restoreAll = true;
        const q = {
          reserveId: team.reserveId,
        }
        this.$http.post(this.apiRestoreSelectRoom.url, JSON.stringify(q), {
          headers: {"Content-Type": "application/json; charset=utf-8"}
        })
          .done(res => {
            if (!res.success) {
              dsf.layer.message(res.message, false);
              return
            }
            dsf.layer.message("");
            this.queryRoomList();
            eventBus.emit(eventBusEvent.refreshGroupList);
          })
          .error(err => {
            dsf.layer.message("", false)
          })
          .always(() => {
            btnLoading.restoreAll = false;
          })
      })
        .catch(() => {
        })
    },
    GetAllBuildList() {
      this.$http.get(this.apiBuildingList.url, {
        type: 1
      }).done(res => {
        this.buildList = res.data;
      })
    },
    changeBuildCheckGroup(checkList) {
      const callback = (list, obj) => {
        console.log(list)
        console.log(obj)
        //
        this.form.floorIds = this.form.floorIds.filter(id => !!obj[id]);
        this.rowList = list
      }
      getRowList.call(this, checkList, callback);
    },
    saveAuto(btnLoading) {
      //
      if (!this.form.buildingIds || this.form.buildingIds.length === 0) {
        dsf.layer.message("", false);
        return
      }
      //
      if (!this.form.reserveIds || this.form.reserveIds.length === 0) {
        dsf.layer.message("", false)
        return
      }
        
      //
      if (!judgeTeamTimeArrange.call(this)) return;
        
      if (!teamIsNotEmpty.call(this)) {
        dsf.layer.message("", false)
        return;
      }
        
      btnLoading.save = true
      const teamNames = [];
      this.personList.forEach(a => {
        if (this.form.reserveIds.includes(a.reserveId)) {
          teamNames.push(a.teamName)
        }
      });
      const q = {
        ...this.form,
        ...this.timeForm,
        teamNames: teamNames
      }
      this.$http.post(this.apiAutoCheckIn.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return
          }
          this.autoDialogVisible = false;
          dsf.layer.message("");
          this.queryRoomList();
          eventBus.emit(eventBusEvent.refreshGroupList);
        })
        .error(err => {
          dsf.layer.message("", false)
        })
        .always(() => {
          btnLoading.save = false;
        })
    },
    changeRuleCheck(status, item) {
      if (!status) {
        this.form.rules = this.form.rules.filter(a => a !== item.val);
      } else {
        if (!item.atomicity) {
          this.form.rules.push(item.val)
          return;
        }
          
        const arr = [];
        this.rulesByAuto.forEach(a => {
          if (a.atomicity === item.atomicity) arr.push(a.val)
        });
        this.form.rules = this.form.rules.filter(a => !arr.includes(a))
        this.form.rules.push(item.val)
      }
    },
    changeTeamSelect(val) {
      const isOK = judgeTeamTimeArrange.call(this);
      if (isOK) {
        //
        if (val.length === 0) {
          this.timeForm = {
            sDate: "",
            eDate: ""
          }
        }
        if (val.length === 1) {
          const team = this.personList.find(a => a.reserveId === val[0])
          this.timeForm = {
            sDate: team.sDate,
            eDate: team.eDate
          }
        }
        return
      }
      this.form.reserveIds.pop()
    }
  }
}
  
function getTeam() {
  if (!this.activeTeamId) return false;
  return this.personList.find(a => a.teamId === this.activeTeamId)
}
  
function getRowList(list, callback) {
  const all = [];
  for (let i = 0; i < list.length; i++) {
    let p = new Promise(resolve => {
      this.$http.get(this.apiBuildingList.url, {
        buildingId: list[i],
        type: 2
      })
        .done(res => {
          const build = this.buildList.find(a => a.buildingId === list[i]);
          resolve({
            floorName: build.name,
            floorItems: res.data
          });
        })
    })
    all.push(p)
  }
  Promise.all(all).then(res => {
    let obj = {};
    for (let i = 0; i < res.length; i++) {
      let item = res[i].floorItems;
      for (let j = 0; j < item.length; j++) {
        let one = item[j];
        if (obj[one.buildingId]) {
          dsf.error("buildingId ")
          return false
        }
          
        obj[one.buildingId] = one;
      }
    }
    callback(res, obj);
  })
}
  
function judgeTeamTimeArrange() {
  //, 
  if (!this.form.reserveIds || this.form.reserveIds.length === 0) {
    return true
  }
  const teams = this.personList.filter(team => {
    return this.form.reserveIds.includes(team.reserveId)
  })
  let team;
  const sDate = teams[0].sDate.replace(/:\d\d$/, "")
  const eDate = teams[0].eDate.replace(/:\d\d$/, "")
  const timeNotPass = teams.some(a => {
    team = a;
    const start = a.sDate.replace(/:\d\d$/, "")
    const end = a.eDate.replace(/:\d\d$/, "")
    return start !== sDate || end !== eDate
  })
  if (timeNotPass) {
    dsf.layer.message(`${team.teamName}  ${teams[0].teamName} `, false);
    return false
  }
    
  return true
}
  
function teamIsNotEmpty() {
  return this.personList.some(a => {
    if (this.form.reserveIds.includes(a.reserveId)) {
      if (a?.person?.length > 0) {
        //
        return a.person.some(p => +p.occupy === 0)
      }
    }
  })
}
</script>