<template>
  <div class="group-list-warp">
    <el-collapse :value="activeTeamId" :accordion="accordion" @change="changeCollapse">
      <el-collapse-item v-for="(item, it) in personList" :title="item.teamName" :name="item.teamId" :key="item.teamId">
        <div class="group-person-box">
          <ul>
            <li
              v-for="(boy, it) in item.boyList"
              :key="boy.personId"
              :data-id="boy.personId"
              :data-name="boy.personName"
            >
              <img v-if="boy.headImg" :src="boy.headImg" alt="">
              <i v-else class="icon-boy-head"></i>
              <span>{{boy.personName}}</span>
            </li>
            <div v-if="!item.boyList || item.boyList.length === 0"></div>
          </ul>
          <ul>
            <li
              v-for="(girl, it) in item.girlList"
              :key="girl.personId"
              :data-id="girl.personId"
              :data-name="girl.personName"
            >
              <img v-if="girl.headImg" :src="girl.headImg" alt="">
              <i v-else class="icon-girl-head"></i>
              <span>{{girl.personName}}</span>
            </li>
            <div v-if="!item.girlList || item.girlList.length === 0"></div>
          </ul>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>

<script>
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {customAssign, assign} from "_platform/pc/dormitory/func";
import {mapState} from 'vuex'
  
export default {
  inject: ['leftConfigPointer'],
  name: "groupList",
  props: {
    personObj: {
      type: Object,
      default() {
        return {}
      }
    },
    personList: {
      type: Array,
      default() {
        return []
      }
    }
  },
  data() {
    return {
      //
      apiGroupList: null,
      accordion: null,
    }
  },
  computed: {
    leftConfig() {
      return this.leftConfigPointer()
    },
    sexRule() {
      return this.apiGroupList.sexRule;
    },
    ...mapState('dormitory', [
      "activeTeamId"
    ])
  },
  watch: {
    personList(val) {
      let obj = {};
      if (!val || val.length === 0) return obj;
      for (let i = 0; i < val.length; i++) {
        let team = val[i];
        if (!team.person) continue;
        for (let j = 0; j < team.person.length; j++) {
          let person = team.person[j];
            
          if (obj[person.personId]) {
            dsf.layer.alert(" personId ")
            return
          }
          obj[person.personId] = person
        }
      }
      this.$emit("update:personObj", obj);
        
      if (!this.activeTeamId) {
        this.$store.commit("dormitory/activeTeamId", val[0].teamId)
      }
      this.$nextTick(() => {
        this.createInteraction()
      })
    }
  },
  created() {
    // Object.assign(this, this.leftConfig);
    assign(this, this.leftConfig);
    this.queryList();
  },
  mounted() {
    eventBus.on(eventBusEvent.refreshGroupList, () => {
      this.queryList()
    })
  },
  methods: {
    reset() {
      this.queryList();
    },
    queryList() {
      let q = {};
      if (_.isFunction(this.apiGroupList.query)) {
        let add = this.apiGroupList.query();
        q = customAssign(q, add);
      }
      this.$http.get(this.apiGroupList.url, q).done(res => {
        let result = res.data;
        if (_.isFunction(this.apiGroupList.callback)) {
          result = this.apiGroupList.callback(result);
        }
          
        for (let i = 0; i < result.length; i++) {
          let item = result[i];
          item.boyList = [];
          item.girlList = [];
          if (!_.isArray(item.person)) continue;
          for (let j = 0; j < item.person.length; j++) {
            let person = item.person[j];
            if (person.occupy === 1) continue;    //occupy 1 
            if (person.sexValue === this.sexRule.male) item.boyList.push(person);
            if (person.sexValue === this.sexRule.female) item.girlList.push(person);
          }
        }
        this.$emit("update:personList", result)
      })
    },
    changeCollapse(val) {
      this.$store.commit("dormitory/activeTeamId", val);
      this.createInteraction()
    },
    createInteraction() {
      $(".group-person-box li").draggable({
        addClasses: false,
        start: function(evt) {
          console.log("ss")
        },
        helper() {
          let txt = $(this).data("name");
          let id = $(this).data("id");
          return $(`<div class="drag-item" data-id=${id}>${txt}</div>`);
        },
        cursor: "default",
        cursorAt: {
          top: -5,
          left: -5
        },
        stop(evt, ui) {
      
        }
      });
    },
  }
}
</script>