<template>
  <div>
    <div class="top-tool">
      <div class="title">{{title}}</div>
      <div class="sec-box">
        <dsf-button :loading="btnLoading.occupied" @click="preClick('resetOccupiedRoom')"></dsf-button>
        <dsf-button :loading="btnLoading.checked" @click="preClick('resetCheckedRoom')"></dsf-button>
        <dsf-button :loading="btnLoading.sure" @click="preClick('sureRoomResource')"></dsf-button>
        <dsf-button :loading="btnLoading.auto" @click="preClick('autoRoomResource')"></dsf-button>
      </div>
    </div>
    <div class="msg-box">
      <color-state-list :room-state-list="roomStateList"></color-state-list>
      <div class="msg">
        <label></label>
        <span v-for="(item, it) in computeAssign" :key="it">{{item.name}}<b>{{item.num}}</b></span>
      </div>
    </div>
    <div class="msg-box">
      <div class="msg">
        <label></label>
        <span>{{team.teamName}}</span>
        <span class="time">{{team.sDate}}</span>
        <span class="time">{{team.eDate}}</span>
      </div>
      <div class="msg">
        <label></label>
        <span v-for="(item, it) in computeReserve" :key="it">{{item.name}}<b>{{item.num}}</b></span>
        <span>{{allNeedReserve}}</span>
      </div>
      <div class="msg">
        <label></label>
        <span><b class="blue">{{chosenBedNum}}</b></span>
        <span v-for="(item, it) in chosenRoomNum" :key="it">{{item.name}}<b class="blue">{{msgChosenNum[item.code]}}</b></span>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import colorStateList from "_platform/pc/dormitory/room/colorStateList";
import inlineSelectBox from "_platform/pc/dormitory/room/inlineSelectBox";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {customAssign, assign} from "_platform/pc/dormitory/func";
  
export default {
  name: "allocationTopTool",
  inject: ["rightConfigPointer"],
  components: {
    colorStateList,
    inlineSelectBox
  },
  computed: {
    rightConfig(){
      return this.rightConfigPointer()
    },
    roomTypeObj() {
      let obj = {}
      for (let i = 0; i < this.roomTypes.length; i++) {
        let item = this.roomTypes[i];
        if (obj[item.val]) {
          dsf.error("roomTypes val");
          return {}
        }
        obj[item.val] = item
      }
      return obj
    },
    ...mapState('dormitory', [
      "chosenRoomId",
      "roomObj",
      "roomStateList"
    ]),
    chosenRoomNum(){
      return this.roomTypes.filter(a => this.msgChosenNum[a.code] > 0)
    },
    computeReserve(){
      let arr = [];
      for (let i=0; i<this.roomTypes.length; i++){
        let a = this.roomTypes[i];
        let num = this.mReserve[a.code]
        if (!num || num <= 0 ) continue;
        let obj = {
          name: a.name,
          num: num
        }
        arr.push(obj)
      }
      return arr
    },
    computeAssign(){
      let arr = [];
      for (let i=0; i<this.roomTypes.length; i++){
        let a = this.roomTypes[i];
        let num = this.mAssign[a.code]
        if (!num || num <= 0 ) continue;
        let obj = {
          name: a.name,
          num: num
        }
        arr.push(obj)
      }
      return arr
    },
    chosenBedNum(){
      if (_.isArray(this.chosenRoomId) && this.chosenRoomId.length > 0){
        let num = 0;
        this.chosenRoomId.forEach(a => {
          num += parseInt(this.roomObj[a]["beds"])
        })
        return num
      }
      return 0
    },
    team(){
      const q = this.$route.query;
      return {
        teamName: q.name,
        sDate: decodeURIComponent(q.sDate),
        eDate: decodeURIComponent(q.eDate)
      }
    }
  },
  data() {
    return {
      //
      title: "",
      roomTypes: null,
      apiRoomReserveInfo: null,
  
      //
      btnLoading: {
        occupied: false,
        checked: false,
        sure: false,
        auto: false
      },
      msgChosenNum: {},
      mReserve: {},     //
      mAssign: {},      //
      allNeedReserve: null,       //
    }
  },
  watch: {
    chosenRoomId(val) {
      this.computeRoomNum(val)
    }
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
    this.queryReserveInfo();
  },
  methods: {
    preClick(type) {
      let temp = type === "autoRoomResource"? this.mReserve: this.btnLoading;
      eventBus.emit(eventBusEvent[type], temp);
    },
    computeRoomNum(checkedArr) {
      this.msgChosenNum = {};
      for (let i = 0; i < checkedArr.length; i++) {
        let roomId = checkedArr[i]
        let room = this.roomObj[roomId];
        let code = this.roomTypeObj[room.roomType].code;
        this.msgChosenNum[code] = (this.msgChosenNum[code] && this.msgChosenNum[code] > 0) ? this.msgChosenNum[code] + 1 : 1
      }
    },
    queryReserveInfo(){
      let q = {}
      if (_.isFunction(this.apiRoomReserveInfo.query)) {
        const add = this.apiRoomReserveInfo.query();
        q = customAssign(q, add);
      }
      this.$http.get(this.apiRoomReserveInfo.url, q).done(res => {
        this.mReserve = res.data.reserve;
        this.mAssign = res.data.assign;
          
        //
        let a = 0;
        for (let i=0; i<this.roomTypes.length; i++){
          let item = this.roomTypes[i];
          a += parseInt(this.mReserve[item.code])
        }
        this.allNeedReserve = a
      })
    }
  }
}
</script>