<template>
  <ul class="ds-state-list">
    <li v-for="(item, it) in roomStateList" :key="item.val">
      <label @click="checkState(item, it)">
        <i :style="getSty(item)"></i>
        <span>{{item.txt}}</span>
      </label>
    </li>
  </ul>
</template>

<script>
export default {
  name: "colorStateList",
  props: {
    roomStateList: {
      type: Array,
      default() {
        return []
      }
    },
    checkAble: {
      type: Boolean,
      default: true
    }
  },
  created() {
    //
    this.roomStateList.forEach(a => {
      a.checked = true
    })
  },
  methods: {
    getSty(a) {
      return {
        backgroundColor: a.checked ? a.bgColor: "rgba(255,255,255,0)",
        border: a.checked ? "none": "2px solid " + a.color
      }
    },
    checkState(a, index){
      if (!this.checkAble) return;
      const arr = _.cloneDeep(this.roomStateList);
      const temp = Object.assign({}, a);
      temp.checked = !temp.checked
      arr.splice(index, 1, temp)
      this.$store.commit('dormitory/changeRoomStateList', arr)
    },
  }
}
</script>