<template>
  <div class="floor-container">
    <floor-box
      :floor-list="floorList"
      :active-room-id.sync="activeRoomId"
      @click-room="clickRoom"
    ></floor-box>
    <room-history-dialog
      :visible.sync="roomStatusVisible"
      :info="roomStatusInfo"
      @dialog-close="onCloseDialog"
    ></room-history-dialog>
    <action-dialog
      title=""
      :visible.sync="roomMonthVisible"
      :no-footer="true"
      @dialog-close="onCloseDialog"
    >
      <template #inner>
        <div class="month-box">
          <div class="month-title">
            <span>{{monthRoomInfo.title}}</span>
            <span>{{monthRoomInfo.type}}</span>
          </div>
          <div class="month-body">
            <div v-for="(item, it) in monthArr" :key="it" :style="mSty(item)">{{item.text}}</div>
          </div>
        </div>
  
        <div class="month-footer">
          <color-state-list :room-state-list="monthStateList" :check-able="false"></color-state-list>
        </div>
      </template>
    </action-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import roomHistoryDialog from "_platform/pc/dormitory/dialog/roomHistoryDialog";
import floorBox from "_platform/pc/dormitory/room/floorBox";
import actionDialog from "_platform/pc/dormitory/dialog/actionDialog";
import colorStateList from "_platform/pc/dormitory/room/colorStateList";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {customAssign, filterOriginFloor, assign} from "_platform/pc/dormitory/func";
  
export default {
  name: "roomBox",
  inject: ['rightConfigPointer'],
  props: {
    queryTime: {
      type: Object,
      default(){
        return {
          sDate: "",
          eDate: ""
        }
      }
    },
    treeList:{
      type: Array,
    },
    // roomStateList: {
    //   type: Array,
    //   default() {
    //     return [];
    //   }
    // },
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      apiOneMonthRoomInfo: null,
        
      //
      originFloorList: [],
      roomStatusVisible: false,
      roomStatusInfo: {},
      activeRoomId: "",   //roomId
      timeDistance: 0,    //
      timeTicker: null,   //
  
      roomMonthVisible: false,
      monthRoomInfo: {},
      monthArr: [],
    }
  },
  computed: {
    rightConfig(){
      return this.rightConfigPointer()
    },
    mchildren() {
      return this.apiRoomStatus.childrenAlias
    },
    mroomNum() {
      return this.apiRoomStatus.roomNumAlias
    },
    mroomId() {
      return this.apiRoomStatus.roomIdAlias
    },
    monthStateList(){
      return this.roomStateList.slice(0, 3);
    },
    floorList(){
      return filterOriginFloor(this.originFloorList, this.roomStateList)
    },
    ...mapState('dormitory', [
      "currentFloor",
      "roomStateList"
    ])
  },
  components: {
    roomHistoryDialog,
    floorBox,
    actionDialog,
    colorStateList
  },
  watch: {
    currentFloor() {
      this.queryRoomList()
    },
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
  },
  beforeDestroy(){
    if (this.timeTicker){
      clearTimeout(this.timeTicker);
      this.timeTicker = null;
    }
  },
  mounted() {
    eventBus.on(eventBusEvent.queryRoomList, () => {
      this.queryRoomList()
    })
  },
    
  methods: {
    clickRoom(item) {
      this.timeDistance += 1;
        
      if (!this.timeTicker){
        this.timeTicker = setTimeout(()=>{
          if (this.timeDistance >= 2){
            this.dbClickRoom(item);
          }
          else {
            this.singleClickRoom(item);
          }
            
          this.timeDistance = 0
          clearTimeout(this.timeTicker);
          this.timeTicker = null;
        }, 250)
      }
    },
    dbClickRoom(item){
      let q = {
        roomId: item[this.mroomId]
      }
      this.$http.get(this.apiOneMonthRoomInfo.url, q).done(res => {
        if (!res.success){
          dsf.layer.message("\n" + res.message, false);
          return
        }
          
        this.monthRoomInfo = {
          title: `${item.buildingText} / ${item.floorText} / ${item.name}`,
          type: item.roomText
        };
          
        let data = res.data;
        if (_.isFunction(this.apiOneMonthRoomInfo.callback)){
          data = this.apiOneMonthRoomInfo.callback(data)
        }
          
        this.monthArr = data;
        this.roomMonthVisible = true;
      })
    },
    singleClickRoom(item){
      this.roomStatusInfo = {
        roomId: item[this.mroomId],
        roomName: item[this.mroomNum],
        emptyBedNum: item.beds,
        ...this.queryTime
      }
      this.roomStatusVisible = true;
    },
    queryRoomList() {
      let q = {
        ...this.currentFloor,
      };
      if (_.isFunction(this.apiRoomStatus.query)){
        let add = this.apiRoomStatus.query();
        q = customAssign(q, add);
      }
      q = Object.assign({}, q, this.queryTime);
        
      this.$http.get(this.apiRoomStatus.url, q).done(res => {
        this.originFloorList = res.success? res.data: [];
        this.activeRoomId = "";
      })
    },
    onCloseDialog(){
      this.activeRoomId = "";
    },
    mSty(item){
      return {
        backgroundColor: item.bgColor
      }
    }
  }
}
</script>