<template>
  <div class="floor-container allocation">
    <floor-box
      :floor-list="floorList"
      :chosen-room-id.sync="chosenRoomId"
      :active-room-id.sync="activeRoomId"
      :loading-room-id="loadingRoomId"
      :multiple-choose="true"
      @click-room="clickRoom"
    ></floor-box>
    
    <action-dialog
      title=""
      :visible.sync="autoDialogVisible"
      width="30%"
      @save="saveAuto"
    >
      <template #body>
        <el-form ref="form" :model="form" class="auto-checkin-form" label-width="80px">
          <el-form-item v-for="(item, it) in roomTypes" :key="it" :label="item.name">
            <el-input-number v-model="form[item.code]" :controls="false" :precision="0"></el-input-number>
          </el-form-item>
        </el-form>
      </template>
    </action-dialog>
    
    <room-history-dialog
      :visible.sync="roomStatusVisible"
      :info="roomStatusInfo"
      @dialog-close="activeRoomId= ''"
    ></room-history-dialog>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import floorBox from "_platform/pc/dormitory/room/floorBox";
import actionDialog from "_platform/pc/dormitory/dialog/actionDialog";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import ActionDialog from "_platform/pc/dormitory/dialog/actionDialog";
import {customAssign, filterOriginFloor, assign} from "_platform/pc/dormitory/func";
import roomHistoryDialog from "_platform/pc/dormitory/dialog/roomHistoryDialog";
  
export default {
  name: "allocationRoomBox",
  inject: ["rightConfigPointer"],
  props: {
    queryTime: {
      type: Object,
      default() {
        return {
          sDate: "",
          eDate: ""
        }
      }
    },
  },
  data() {
    return {
      //
      apiRoomStatus: null,
      roomTypes: null,
      apiResetRoomStatus: null,
      apiAssignRoomStatus: null,
      apiAutoAssign: null,
        
      //
      startTime: "",
      endTime: "",
      originFloorList: [],
      chosenRoomId: [],     //roomId
      activeRoomId: "",     //roomId
      loadingRoomId: [],
      autoDialogVisible: false,
      form: {},
      roomStatusVisible: false,
      roomStatusInfo: {}
    }
  },
  computed: {
    rightConfig() {
      return this.rightConfigPointer()
    },
    mid() {
      return this.apiRoomStatus.roomIdAlias;
    },
    mnum() {
      return this.apiRoomStatus.roomNumAlias;
    },
    floorList(){
      return filterOriginFloor(this.originFloorList, this.roomStateList)
    },
    ...mapState('dormitory', [
      "currentFloor",
      "roomStateList"
    ]),
  },
  watch: {
    currentFloor() {
      this.queryRoomList()
    },
    roomStateList(){
      this.chosenRoomId = [];
    }
  },
  components: {
    roomHistoryDialog,
    ActionDialog,
    floorBox,
    actionDialog
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
    const currentTime = new Date().Format("yyyy-MM-dd");
    this.startTime = currentTime + " 00:00:00";
    this.endTime = currentTime + " 23:59:59";
  },
  mounted() {
    eventBus.on(eventBusEvent.resetOccupiedRoom, (btnLoading) => {
      this.preResetOccupiedRoom(btnLoading)
    })
    eventBus.on(eventBusEvent.resetCheckedRoom, (btnLoading) => {
      if (!this.chosenRoomId || this.chosenRoomId.length === 0) {
        dsf.layer.message("", false)
        return
      }
      this.resetCheckedRoom(btnLoading)
    })
    eventBus.on(eventBusEvent.sureRoomResource, btnLoading => {
      this.preSureRoomResource(btnLoading)
    })
    eventBus.on(eventBusEvent.autoRoomResource, mRserve => {
      let obj = {};
      for (let i = 0; i < this.roomTypes.length; i++) {
        let item = this.roomTypes[i];
        let num = mRserve[item.code];
        obj[item.code] = (num && +num > 0) ? +num : undefined
      }
      this.form = obj;
      this.autoDialogVisible = true
    })
  },
  methods: {
    clickRoom(room) {
      this.roomStatusInfo = {
        roomId: room[this.mid],
        roomName: room[this.mnum],
        emptyBedNum: room.beds,
        ...this.queryTime
      }
      this.roomStatusVisible = true;
    },
      
    queryRoomList() {
      let q = {
        ...this.currentFloor,
        sDate: this.startTime,
        eDate: this.endTime
      };
      if (_.isFunction(this.apiRoomStatus.query)) {
        let add = this.apiRoomStatus.query();
        q = customAssign(q, add);
      }
        
      this.$http.get(this.apiRoomStatus.url, q).done(res => {
        this.originFloorList = res.success ? res.data : [];
        this.chosenRoomId = [];
      })
    },
    preResetOccupiedRoom(btnLoading) {
      dsf.layer.confirm({
        message: ""
      }).then(() => {
        this.resetOccupiedRoom(btnLoading);
      })
        .catch(() => {
        })
    },
    resetOccupiedRoom(btnLoading) {
      btnLoading.occupied = true;
      let q = {};
      if (_.isFunction(this.apiResetRoomStatus.query)) {
        const add = this.apiResetRoomStatus.query();
        q = Object.assign({}, q, add)
      }
        
      this.$http.post(this.apiResetRoomStatus.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return;
          }
          dsf.layer.message("");
          this.queryRoomList();
        })
        .error(err => {
          dsf.layer.message("", false);
        })
        .always(() => {
          btnLoading.occupied = false
        })
    },
    resetCheckedRoom(btnLoading) {
      btnLoading.checked = true;
      this.loadingRoomId = this.chosenRoomId;
      let q = {
        roomIds: this.chosenRoomId
      };
      if (_.isFunction(this.apiResetRoomStatus.query)) {
        const add = this.apiResetRoomStatus.query();
        q = Object.assign({}, q, add)
      }
        
      this.$http.post(this.apiResetRoomStatus.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return;
          }
          dsf.layer.message("");
          this.queryRoomList();
        })
        .error(err => {
          dsf.layer.message("", false);
        })
        .always(() => {
          btnLoading.checked = false;
          this.loadingRoomId = [];
        })
    },
    preSureRoomResource(btnLoading) {
      if (!this.chosenRoomId || this.chosenRoomId.length === 0) {
        dsf.layer.message("", false)
        return
      }
        
      const roomObjs = getRoomObjs(this.floorList, this.mid);
      const passRoomId = [];
      let couldRooms = "";    //
      let unRooms = "";       //
      this.chosenRoomId.forEach(id => {
        const room = roomObjs[id];
        if (room.statusValue !== 1) {
          unRooms += "<span>" + room[this.mnum] + "</span>";
        } else {
          passRoomId.push(id);
          couldRooms += "<span>" + room[this.mnum] + "</span>";
        }
      })
        
      //
      if (unRooms.length === 0) {
        this.sureRoomResource(btnLoading);
        return
      }
        
      const unHtml = `<p class="room-msg un-pass">${unRooms}</p>`;
      const passHtml = `<p class="room-msg pass">${couldRooms}</p>`;
      const msg = passRoomId.length === 0 ? "" : `${passHtml}`
      dsf.layer.confirm({
        message: `${unHtml}${msg}`,
        customClass: "dormitory-restore-select-room-confirm"
      }).then(() => {
        this.chosenRoomId = passRoomId;
        if (passRoomId.length > 0){
          this.sureRoomResource(btnLoading);
        }
      }).catch(() => {
        console.log("")
      })
    },
    sureRoomResource(btnLoading) {
      btnLoading.sure = true;
      this.loadingRoomId = this.chosenRoomId;
      let q = {
        roomIds: this.chosenRoomId
      };
      if (_.isFunction(this.apiAssignRoomStatus.query)) {
        const add = this.apiAssignRoomStatus.query();
        q = Object.assign({}, q, add)
      }
        
      this.$http.post(this.apiAssignRoomStatus.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return;
          }
          dsf.layer.message("");
          this.queryRoomList();
        })
        .error(err => {
          dsf.layer.message("", false);
        })
        .always(() => {
          btnLoading.sure = false;
          this.loadingRoomId = [];
        })
    },
    saveAuto(btnLoading) {
      btnLoading.save = true;
      const arr = [];
      for (let i = 0; i < this.roomTypes.length; i++) {
        let room = this.roomTypes[i];
        if (!this.form[room.code] || parseInt(this.form[room.code]) <= 0) continue;
        let obj = {
          type: room.val,
          amount: this.form[room.code]
        }
          
        arr.push(obj);
      }
      let q = {
        roomTypes: arr
      }
      if (_.isFunction(this.apiAutoAssign.query)) {
        const add = this.apiAutoAssign.query();
        q = Object.assign({}, q, add)
      }
        
      this.$http.post(this.apiAutoAssign.url, JSON.stringify(q), {
        headers: {"Content-Type": "application/json; charset=utf-8"}
      })
        .done(res => {
          if (!res.success) {
            dsf.layer.message(res.message, false);
            return;
          }
          this.autoDialogVisible = false;
          dsf.layer.message("");
          this.queryRoomList();
        })
        .error(err => {
          dsf.layer.message("", false);
        })
        .always(() => {
          btnLoading.save = false;
        })
        
    },
  }
}
  
function getRoomObjs(floorList, roomId) {
  const obj = {};
  for (let i = 0; i < floorList.length; i++) {
    const floor = floorList[i];
    for (let j = 0; j < floor.roomDtos.length; j++) {
      const room = floor.roomDtos[j];
      obj[room[roomId]] = room
    }
  }
  return obj
}
</script>