<template>
  <div class="date-box">
    <el-date-picker v-model="form.sDate" type="datetime" placeholder="" :readonly="this.readonlyArr[0]" value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm" @change="changeTime1" :picker-options="pickerOption1">
    </el-date-picker>
    <span class="time-divider"></span>
    <el-date-picker v-model="form.eDate" type="datetime" placeholder="" :readonly="this.readonlyArr[1]" value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm" :picker-options="pickerOption2">
    </el-date-picker>
  </div>
</template>

<script>
export default {
  name: "timeRange",
  props: {
    form: {
      type: Object,
      default() {
        return {
          sDate: "",
          eDate: ""
        };
      }
    },
    readonlyArr: {
      type: Array,
      default() {
        return [false, false];
      }
    },
    onlyChooseFuture: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pickerOption1: {
        disabledDate: this.disabledDate1
      },
      pickerOption2: {
        disabledDate: this.disabledDate2
      }
    };
  },
  computed: {
    time1() {
      return new Date(this.form.sDate).getTime();
    },
    time2() {
      return new Date(this.form.eDate).getTime();
    }
  },
  methods: {
    disabledDate1(date) {
      if (!this.onlyChooseFuture) return false;
      let toDay = new Date().Format("yyyy-MM-dd");
      toDay = new Date(toDay + " 00:00:00").getTime();
      const time = new Date(date).getTime();
      return time < toDay;
    },
    disabledDate2(date) {
      const temp = new Date(date).getTime();
      return temp < this.time1;
    },
    changeTime1() {
      if (this.time1 >= this.time2) {
        this.form.eDate = "";
      }
    }
  }
};
</script>