<template>
  <el-dialog
    :visible.sync="visible"
    :width="width"
    :before-close="closeDialog"
    :append-to-body="true"
    :lock-scroll="true"
    custom-class="dsf-dormitory-dialog action-dialog"
    :show-close="showClose"
    :top="top"
  >
    <template #title>
      <div class="bg-title">
        <span>{{title}}</span>
        <div class="top-tool">
          <slot name="topTool"></slot>
        </div>
      </div>
    </template>
    <div class="custom-body">
      <slot name="body"></slot>
    </div>
    <slot name="inner"></slot>
    <div class="floor-container">
      <slot name="floorBox"></slot>
    </div>
    <template v-if="!noFooter" #footer>
      <div class="action-footer">
        <dsf-button @click="closeDialog"> </dsf-button>
        <dsf-button @click="save" :loading="btnLoading.save"> </dsf-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
export default {
  name: "actionDialog",
  props:{
    title:{
      type: String,
      required: true
    },
    visible: {
      type: Boolean,
      default: false,
      required: true
    },
    width: {
      type: String,
      default: "40%"
    },
    showClose:{
      type: Boolean,
      default: true
    },
    top:{
      type: String,
      default: "15vh"
    },
    noFooter:{
      type: Boolean,
      default: false
    },
  },
  data(){
    return{
      btnLoading: {
        save: false
      }
    }
  },
  methods:{
    closeDialog(){
      this.$emit("dialog-close");
      this.$emit("update:visible", false);
    },
    save(){
      this.$emit("save", this.btnLoading);
    },
  }
}
</script>