<template>
  <div class="top-tool">
    <div class="title">{{title}}</div>
    <div class="sec-box">
      <color-state-list :room-state-list="roomStateList"></color-state-list>
      <time-range :only-choose-future="!queryTimeCouldChooseHistory" :form="queryTime"></time-range>
      <dsf-button @click="doQuery"></dsf-button>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import eventBus from "_platform/designer/seat/util/eventBus";
import timeRange from "_platform/pc/dormitory/dialog/timeRange";
import colorStateList from "_platform/pc/dormitory/room/colorStateList";
import {assign} from "_platform/pc/dormitory/func";
  
export default {
  name: "guestsTopTool",
  inject: ['rightConfigPointer'],
  props: {
    queryTime: {
      type: Object,
      default() {
        return {
          sDate: "",
          eDate: "",
        }
      }
    },
  },
  computed:{
    ...mapState('dormitory', [
      'roomStateList'
    ]),
    rightConfig(){
      return this.rightConfigPointer()
    }
  },
  data() {
    return {
      //
      title: "",
      queryTimeCouldChooseHistory: false
    }
  },
  components: {
    timeRange,
    colorStateList
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
  },
  methods: {
    doQuery() {
      eventBus.emit(eventBusEvent.queryRoomList);
    }
  }
}
</script>