<template>
  <div>
    <div class="top-tool">
      <div class="title">{{title}}</div>
      <div class="sec-box">
        <dsf-button :loading="btnLoading.checkOut" @click="preCheckOut"></dsf-button>
        <dsf-button v-if="predis === '1'" :loading="btnLoading.cancel" @click="preCancel"></dsf-button>
        <dsf-button :loading="btnLoading.restore" @click="preRestore"></dsf-button>
        <dsf-button :loading="btnLoading.restoreAll" @click="preRestoreAll"></dsf-button>
        <dsf-button :loading="btnLoading.auto" @click="preAuto"></dsf-button>
      </div>
    </div>
    <div class="state-box">
      <color-state-list :room-state-list="roomStateList"></color-state-list>
      <inline-select-box></inline-select-box>
    </div>
    <div v-if="team" class="msg-box">
      <div class="msg">
        <label></label>
        <span>{{team.teamName}}</span>
        <span class="time">{{team.sDate}}</span>
        <span class="time">{{team.eDate}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import colorStateList from "_platform/pc/dormitory/room/colorStateList";
import inlineSelectBox from "_platform/pc/dormitory/room/inlineSelectBox";
import eventBus from "_platform/designer/seat/util/eventBus";
import {eventBusEvent} from "_platform/pc/dormitory/constant";
import {assign} from "_platform/pc/dormitory/func";
  
export default {
  name: "groupTopTool",
  inject: ['dormitoryRoot', 'rightConfigPointer'],
  props: {
    personList:{
      type: Array,
      default(){
        return []
      }
    }
  },
  components: {
    colorStateList,
    inlineSelectBox
  },
  data() {
    return {
      //
      title: "",
      apiBuildingList: null,
        
      //
      btnLoading:{
        checkOut: false,
        cancel: false,
        restore: false,
        restoreAll: false,
        auto: false
      }
    }
  },
  computed:{
    rightConfig(){
      return this.rightConfigPointer()
    },
    predis(){
      return dsf.url.queryString("predis")
    },
    ...mapState('dormitory', [
      'roomStateList',
      'activeTeamId'
    ]),
    team(){
      let team = null;
      for (let i=0; i<this.personList.length; i++){
        let a = this.personList[i];
        if (a.teamId === this.activeTeamId){
          team = Object.assign({}, a);
          team.sDate = a.sDate.replace(/:\d\d$/, "");
          team.eDate = a.eDate.replace(/:\d\d$/, "");
          break;
        }
      }
      return team
    },
  },
  created() {
    // Object.assign(this, this.rightConfig);
    assign(this, this.rightConfig, ['roomStateList']);
  },
  methods: {
    preCheckOut() {
      eventBus.emit(eventBusEvent.chosenRoomCheckOut, this.btnLoading);
    },
    preCancel() {
      eventBus.emit(eventBusEvent.reserveRoomCancel, this.btnLoading)
    },
    preRestore() {
      eventBus.emit(eventBusEvent.restoreSelectRoom, this.btnLoading);
    },
    preRestoreAll() {
      let floorList = this.dormitoryRoot.$refs.room?.floorList;
      //  
      if (dsf.config.setting_sgcp_reserve == 1 && !floorList?.length) {
        dsf.layer.message('', false);
        return;
      }
      eventBus.emit(eventBusEvent.restoreAllRoom, this.btnLoading);
    },
    preAuto() {
      eventBus.emit(eventBusEvent.autoCheckIn, this.btnLoading)
    },
      
  }
}
</script>